<template>
<!--  -->
  <div>
    <!-- myCentermycenteropenNames1-1
    ,mycentermycenter -->
    <myCenter :bread="breadText" :openNames="openNames"></myCenter>
    <div class="center-right">
      <ul v-if="!showMsg" class="jm-ul">
      
        <li class="jm-li" v-for="(item, index) in imgList" :key="index">
            <span>{{item.mallId}}</span>
          <img
            class="jm-img"
           :src="item.imgUrl"
          />

          <!--  -->
          <div
            @mouseenter="enter(index)"
            @mouseleave="leave(index)"
            class="jm-mask"
            :class="current == index ? jmMaskOpacity : ''"
          >
            <Icon
              size="35"
              class="jm-bigger"
              type="ios-add-circle-outline"
              @mouseenter.native="enterIcon(index)"
              @mouseleave.native="leaveIcon(index)"
            />
            <Button
              @click="gotoResumeTemplate(item.mallId)"
              class="jm-btn"
              shape="circle"
              icon="ios-search"
              ></Button
            >
            <Button
              @click="cancle(index,item.mallId)"
              class="jm-btn jm-btn-delete"
              shape="circle"
              icon="ios-trash"
              ></Button
            >
          </div>
          <div class="jm-preview" v-show="currentIcon == index">
            <img
              :class="isRightImg ? 'rightPreview' : 'leftPreview'"
              :src="item.imgUrl"
            />
          </div>
        </li>
      </ul>
      <div v-if="showMsg" class="msg-info">
        
        <span class="msg-router" @click="gotoTemplateMall"
          ></span
        >
        
      </div>
    </div>
  </div>
</template>

<script>
import myCenter from "./MyCenter";
import NavHeader from "@/components/NavHeader";
import NavFooter from "@/components/NavFooter";
import Bus from "@/assets/event-bus.js";

export default {
  name: "collection",
  data() {
    return {
      imgList: [], //
      jmMaskOpacity: "", //
      current: 0, //
      currentIcon: -1, //
      isRightImg: false, //
      showMsg: false, //
      breadText:"",//
      openNames:"1-2"//
    };
  },
  components: {
    myCenter: myCenter,
      NavHeader,
    NavFooter,
  },
  methods: {
    //
    cancle(index,name) {
       this.axios.post("collections/resume", {
        userName: localStorage.getItem("userName"), //vuex
        mallId:name
      }).then(res => {
        if(res.data.status=="2"){
          this.imgList.splice(index,1)
          if(this.imgList==""){
             this.showMsg = true;
             return
           }else{
             this.showMsg = false;
           }
        }
         
        })
        .catch(err => {
          console.log("err", err);
        });
    },
    // 
    gotoTemplateMall() {
      this.$router.push({
        // name: "writeResumeTemplate"+(index+1)
        name: "templateMall"
      });
    },
    // 
    gotoResumeTemplate(index) {
      this.$router.push({
        // name: "writeResumeTemplate"+(index+1)
        name: "writeResumeIde",
        query: {
          id: index
        }
      });
      // 
      window.location.reload();
    },
    // 
    enter(index) {
      this.current = index;
      this.jmMaskOpacity = "jmMaskOpacity";
    },
    //
    leave(index) {
      this.current = index;
      this.jmMaskOpacity = "";
    },
    // 
    enterIcon(index) {
      this.isRightImg =
        index == parseInt(index / 4) * 4 + 2 ||
        index == parseInt(index / 4) * 4 + 3;
      this.currentIcon = index;
    },
    //
    leaveIcon(index) {
      this.currentIcon = -1;
    },

    // 
    getImgList() {
      this.axios
        .get("collections/getResume", {
          params: {
         userName: localStorage.getItem("userName") //vuex
        }
        })
        .then(res => {
          if (res.data.status == "0") {
          this.imgList = res.data.result;
          //  null.xxx
           if(!this.imgList || this.imgList==""){ 
             this.showMsg = true;
             return
           }else{
             this.showMsg = false;
           }
          }
        })
        .catch(err => {});
    }
  },
  mounted() {
    this.getImgList();
      Bus.$on("postOpenNames", (data) => {
      this.openNames = data
      
    })
  }
};
</script>

<style lang="less" scoped>
.center-right {
  position: absolute;
  top: 80px;
  left: 240px;
  margin: 10px;
  .jm-ul {
    margin-top: 20px;
    .jm-li {
      text-align:  center;
      width: 25%;
      float: left;
      .jm-img {
       background: #ddd;
        height: 350px;
        width: 250px;
        padding: 10px;
      }
    }
  }
}
// 
.jm-mask {
  position: relative;
  text-align: center;
  height: 350px;
  width: 250px!important;
  padding: 10px;
  margin-top: -356px;
  margin-left: 15px;
  float: left;
  background-color: rgba(0, 192, 145, 0.5);
  opacity: 0;
  width: 100%;
  left: 0;
  right: 0;
  // bottom: 0;
  .jm-btn {
    position: relative;
    top: 50%;
    font-size: 20px;
    transform: translate(0, -50%);
  }
  .jm-bigger {
    position: absolute;
    right: 0;
    color: #fff;
  }
}
.jmMaskOpacity {
  opacity: 0.8 !important;
}
// 
.msg-info {
  margin-left: 60px;
  margin-top: 20px;
  text-align: center;
  font-size: 16px;
  color: #666;
  .msg-router {
    color: blue;
    cursor: pointer;
  }
}
  // 
  .jm-preview {
    position: relative;
   
    img {
       z-index: 100;
      position: absolute;
      background: #ddd;
      padding: 10px;
      width: 500px;
      height: 730px;
      top: -400px; 
      left: 380px;
    }
  }
    .leftPreview {
    position: relative;
    margin-left: -100px;
  }
  .rightPreview {
    position: absolute;
    margin-left: -880px;
  }
  .jm-btn-delete{
    margin-top: 10px;
    color: red;
  }
</style>
