
<template>
  <!--  -->
  <div>
    <Modal class="jm-skill-modal" v-model="modalSkills">
      <p class="jm-title" slot="header" style="text-align:left">
        <span></span>
      </p>
      <div class="jm-center">
        <Row class="jm-input">
          <Input
            v-model="customSkiVal"
            placeholder="10"
            clearable
            style="width: 465px"
          />
          <Button
            style="background:#00c091;color:#fff;width:80px;margin-left:10px;"
            @click="addSkills"
            ></Button
          >
        </Row>
        <Row class="jm-ski-table">
          <div class="jm-Recom-title"></div>
          <div class="jm-recom-body">
            <!-- on-change@ -->
            <Tag
              v-for="(item, index) in skillList"
              :key="index"
              style="border:1px solid #ddd;margin:0 10px 8px;"
              checkable
              :checked="item.isChecked"
              :name="item.name"
              @on-change="changeTag(item.isChecked, item.name, index)"
              color="success"
              >{{ item.name }}</Tag
            >
          </div>
        </Row>
        <Row class="jm-ski-table">
          <div class="jm-Recom-title">
            
            <span class="jm-ski-info">8</span>
          </div>
          <div class="jm-recom-list">
            <Row
              class="jm-recom-line"
              v-for="(item, index) in hasSkillList"
              :key="index"
            >
              <Col v-if="item.isChecked" span="8" style="text-align: left;">
                <Icon
                  @click="deleteSkills(index)"
                  class="jm-recom-icon"
                  size="20"
                  type="md-close-circle"
                />
                <span class="jm-recom-text">{{ item.name }}</span>
              </Col>
              <Col v-if="item.isChecked" span="12">
                <Slider
                  v-model="item.skillNumber"
                  :step="33"
                  :tip-format="format"
                ></Slider>
              </Col>
              <Col
                v-if="item.isChecked"
                span="4"
                class="jm-recom-tag"
                style="text-align: right;"
                >{{ item.skillNumber | ConversionNumber }}</Col
              >
            </Row>
          </div>
        </Row>
      </div>
      <div class="jm-footer" slot="footer">
        <Button @click="save" class="jm-save-btn" size="large"></Button>
        <Button @click="cancelModel" class="jm-cancle-btn" size="large"
          ></Button
        >
      </div>
    </Modal>
  </div>
</template>
<script>
export default {
  name: "skillModal",
  props: ["modalSkills", "hasSkillLists"], //
  filters: {
    ConversionNumber: function(val) {
      //
      if (val == 0) {
        return "";
      } else if (val == 33) {
        return "";
      } else if (val == 66) {
        return "";
      } else if (val == 99) {
        return "";
      }
    }
  },

  data() {
    return {
      customSkiVal: "", //
      valRecom: 66, //
      showTipText: "", //
      sliderText: "", //
      hasSkillList: [
        { name: "", isChecked: false, skillNumber: 66 },
        { name: "BD", isChecked: false, skillNumber: 66 },
        { name: "", isChecked: false, skillNumber: 66 },
        { name: "", isChecked: false, skillNumber: 66 }
      ], //
      skillList: [
        { name: "", isChecked: false, skillNumber: 66 },
        { name: "BD", isChecked: false, skillNumber: 66 },
        { name: "", isChecked: false, skillNumber: 66 },
        { name: "", isChecked: false, skillNumber: 66 }
      ] //
    };
  },
  watch: {
    // 
    // skillList: {
    //   handler(newValue, oldValue) {
    //     // var obj = { name: "" };
    //     for (var i in newValue) {
    //       if (newValue[i].isChecked) {
    //         // obj.name = newValue[i].name;
    //         // this.hasSkillList.push(newValue);
    //         this.hasSkillList.find(
    //           item => item.name === newValue[i].name
    //         ).isChecked = true;
    //       } else {
    //         this.hasSkillList.find(
    //           item => item.name === newValue[i].name
    //         ).isChecked = false;
    //       }
    //     }
    //   },
    //   deep: true
    // },
    hasSkillLists: {
      handler(newValue, oldValue) {
        this.hasSkillList = this.hasSkillLists;
        var that = this;
        this.hasSkillList.forEach((item, index) => {
          that.skillList.forEach((item1, index1) => {
            if (item.name == item1.name) {
              that.skillList[index1].isChecked = item.isChecked;
            }
          });
        });
      },
      deep: true
    }
  },
  methods: {
    //
    format(val) {
      if (val == 0) {
        return "";
      } else if (val == 33) {
        return "";
      } else if (val == 66) {
        return "";
      } else if (val == 99) {
        return "";
      }
    },
    changeTag(isChecked, name, index) {
      var that = this;
      var slength = this.hasSkillList.length;
      for (var i = 0; i < slength; i++) {
        if (this.hasSkillList[i].name == name) {
          this.skillList[index].isChecked = !isChecked;
          this.hasSkillList[i].isChecked = !isChecked;
          return;
        }
      }
      let addSkillContent = {
            name: name,
            isChecked: true,
            skillNumber: 66
          };
          this.hasSkillList.push(addSkillContent);
    },
    // 
    addSkills() {
      if (this.customSkiVal === "") {
        this.$Message.warning("");
        return;
      } else if (
        // ,truefalsetrue
        JSON.stringify(this.hasSkillList).indexOf(this.customSkiVal) != -1
      ) {
        if (
          this.hasSkillList.find(item => item.name === this.customSkiVal)
            .isChecked
        ) {
          this.$Message.warning("");
          return;
        } else {
          this.hasSkillList.find(
            item => item.name === this.customSkiVal
          ).isChecked = true;
          this.hasSkillList.find(
            item => item.name === this.customSkiVal
          ).skillNumber = 66;
          return;
        }
      }

      let addSkillContent = {
        name: this.customSkiVal,
        isChecked: true,
        skillNumber: 66
      };
      this.hasSkillList.push(addSkillContent);
    },
    // 
    deleteSkills(i) {
      this.hasSkillList[i].isChecked = false;
      // skillList
      if (
        JSON.stringify(this.skillList).indexOf(this.hasSkillList[i].name) != -1
      ) {
        // tag
        this.skillList.find(
          item => item.name === this.hasSkillList[i].name
        ).isChecked = false;
        // 
        this.hasSkillList[i].skillNumber = 66;
      }
    },
    // 
    cancelModel() {
      this.$emit("changeSkillModel", false);
    },
    // 
    save() {
      this.cancelModel();
      // fasle
      let choseSkillList = [];
      this.hasSkillList.forEach((item, index) => {
        if (item.isChecked) {
          choseSkillList.push(item);
        }
      });
      this.$emit("saveSkill", choseSkillList);
    }
  }
};
</script>
 <style lang="less" scoped>
.jm-skill-modal {
  width: 600px;
  /deep/.ivu-modal {
    width: 600px !important;
  }
  // deep
  /deep/.ivu-modal-header {
    padding: 0 !important;
  }
  /deep/.ivu-slider-bar {
    height: 8px;
    background: #e6e8ef;
    border-radius: 6px;
    position: absolute;
  }
  /deep/.ivu-slider-button {
    width: 14px;
    height: 14px;
    border: 2px solid #00c901;
  }
  /deep/.ivu-slider-wrap {
    height: 8px;
  }
  // 
  .jm-title {
    width: 100%;
    height: 40px;
    line-height: 40px;
    padding-left: 20px;
    color: white;
    background-color: #00c190;
    border-radius: 4px 4px 0 0;
  }
  // 
  .jm-center {
    .jm-ski-table {
      padding: 4px 7px;
      .jm-Recom-title {
        text-align: left;
        margin-top: 10px;
        margin-bottom: 5px;
        font-size: 14px;
        .jm-ski-info {
          font-size: 12px;
          color: #ccc;
        }
      }
      .jm-recom-body {
        text-align: left;
        width: 100%;
        height: 160px;
        padding: 10px 8px;
        border: solid 1px #ccc;
        border-radius: 2px;
        overflow-y: auto;
      }
      .jm-recom-list {
        .jm-recom-line {
          height: 34px;
          line-height: 34px;
          margin-bottom: 10px;
          text-align: center;
          .jm-recom-icon {
            color: #80e0c7;
          }
          .jm-recom-icon:hover {
            color: red;
            cursor: pointer;
          }
          .jm-recom-text {
            display: inline-block;
            height: 26px;
            line-height: 26px;
            padding: 0 12px;
            color: white;
            background-color: #7fe0c7;
            border-radius: 13px;
            margin-left: 30px;
            font-size: 12px;
            //   vertical-algin:middle;
          }
          .jm-recom-tag {
            color: #414a60;
            font-size: 16px;
          }
        }
      }
    }
  }
  // 
  .jm-footer {
    text-align: center;
    .jm-save-btn {
      background-color: #00c190;
      color: white;
      border-radius: 4px;
      border: none;
      width: 100px;
      height: 40px;
      margin-left: 15px;
      cursor: pointer;
    }
    .jm-cancle-btn {
      background-color: #ededed;
      color: #8b8b8b;
      border-radius: 4px;
      border: none;
      width: 100px;
      height: 40px;
      margin-left: 15px;
      cursor: pointer;
    }
  }
}
</style>