<template>
  <!--  -->
  <div>
    <vuedraggable class="wrapper">
      <rightContent1
        :themeList="jobThemeList"
        themeFlag="jobIntention"
        key="1"
        :jobIntentionLists="jobIntentionList"
        name="jobIntention"
        v-on:savaJobIntention="savaJobIntention"
        title=""
      >
        <div slot="slotRight">
          <Row
            class="jobList"
            v-for="(item, index) in jobIntentionList"
            :key="index">
            <Icon :size="20" class="jobIcon" :type="item.type" />
            <span class="jobText">{{ item.baseText }}</span>
          </Row>
        </div>
      </rightContent1> 
      <!--  -->
       <rightContent1
        :themeList="eduThemeList"
        themeFlag="edu"
        :key="2"
        name="edu"
        v-on:addEdus="addEdus('edu')"
        title=""
      >
        <div slot="slotRight">
          <Row v-for="(item, index) in eduList" :key="index">
            <Row>
              <Row class="edu-row"> 
                <Col v-if="settingObj.isShowJobTime" :span="8" class="jm-edu-col">
                  <Input
                    v-model="item.eduDate"
                    placeholder=":2016/08-2017/09"
                  />
                </Col>
                <Col :span="8" class="jm-edu-col">
                  <Input v-model="item.schooName" placeholder="" />
                </Col>
               
                <Col :span="8" class="jm-edu-col">
                  <Input v-model="item.majorName" placeholder="" />
                </Col>
            
              </Row>
              <!-- <Row>
                <Col :span="18" class="jm-edu-col">
                  <Input v-model="item.majorName" placeholder="" />
                </Col>
              </Row> -->
            </Row>
            <Row class="edu-row">
              <Col class="jm-edu-col">
                <Input
                  v-model="item.eduContent"
                  type="textarea"
                  clearable
                  autosize
                  class="input-introduce"
                  placeholder=""
                />
              </Col>
            </Row>
          </Row>
        </div>
      </rightContent1> 
      <!--  -->
     <rightContent1
        :themeList="workThemeList"
        themeFlag="work"
        key="3"
        name="edu"
        v-on:addEdus="addEdus('experience')"
        title=""
      >
        <div slot="slotRight">
          <Row v-for="(item, index) in experienceList" :key="index">
            <Row class="edu-row">
              <Col :span="8" class="jm-edu-col">
                <Input
                  v-model="item.date"
                  placeholder=":2016/08-2017/09"
                />
              </Col>
              <Col :span="8" class="jm-edu-col">
                <Input v-model="item.name" placeholder="" />
              </Col>
                <Col :span="8" class="jm-edu-col">
                <Input v-model="item.positionName" placeholder="" />
              </Col>
            </Row>
            <!-- <Row>
              <Col :span="18" class="jm-edu-col">
                <Input v-model="item.positionName" placeholder="" />
              </Col>
            </Row> -->
            <Row class="edu-row">
              <Col class="jm-edu-col">
                <Input
                  v-model="item.content"
                  clearable
                  autosize
                  type="textarea"
                  class="input-introduce"
                  placeholder=""
                />
              </Col>
            </Row>
          </Row>
        </div>
      </rightContent1>
      <!--  -->
      <rightContent1
        :themeList="ThemeAppraiseList"
        themeFlag="appraise"
        key="4"
        name="appraise"
        title=""
      >
        <div slot="slotRight">
          <Row class="edu-row">
            <Col class="jm-edu-col">
              <Input
                v-model="selfEvaluation"
                placeholder="500"
                clearable
                autosize
                type="textarea"
                class="input-introduce"
              />
            </Col>
          </Row>
        </div>
      </rightContent1>
    </vuedraggable>
    <!-- showFind!=='1' -->
    <div v-if="showFind!=='1'">
      <FindResumeModal :modal2="modal2" :modal_loading="modal_loading"></FindResumeModal>
      </div>
  </div>
</template>
<script>
import vuedraggable from "vuedraggable";
import rightContent1 from "./RightContent1";
import Bus from "@/assets/event-bus.js";
import FindResumeModal from "./FindResumeModal";
export default {
  name: "mainContent",
  components: {
    vuedraggable,
    rightContent1,
    FindResumeModal
  },
  props:["formData","showFind"],
 
  data() {
    return {
      modal2: false,
      modal_loading: false,
      jobThemeList: {}, //
      eduThemeList: {}, //
      workThemeList: {}, //
      ThemeAppraiseList: {}, //
      jobIntentionList: [
        //
        {
          type: "ios-contact",
          baseText: "",
          name: "choseJob"
        },
        {
          type: "md-briefcase",
          baseText: "",
          name: "city"
        },
        {
          type: "ios-mail",
          baseText: "",
          name: "entryTime"
        },
        {
          type: "ios-call",
          baseText: "",
          name: "salary"
        }
      ],
      // 
      eduList: [
        {
          eduDate: "",
          schooName: "",
          majorName: "",
          eduContent: ""
        }
      ],
      // 
      experienceList: [
        {
          date: "",
          name: "",
          positionName: "",
          content: ""
        }
      ],
      // 
      selfEvaluation: "",
       settingObj: {
        isShowJobTime: true
      }, //
      // nameId:0,//id
    };
  },
  watch: {},
  methods: {
       // 
    saveThemes(){
       Bus.$on("saveTheme", themeList => {
      var flags = themeList[1]; //
      console.log(themeList, "");
      if (flags == "jobIntention") {
        this.jobThemeList = themeList[0];
      } else if (flags == "edu") {
        this.eduThemeList = themeList[0];
      } else if (flags == "work") {
        this.workThemeList = themeList[0];
      } else if (flags == "appraise") {
        this.ThemeAppraiseList = themeList[0];
      }
    });
    },
    // 
    savaJobIntention(val) {
      this.jobIntentionList.find(item => item.name == "choseJob").baseText =
        val.choseJob;
      this.jobIntentionList.find(item => item.name == "city").baseText =
        val.city;
      this.jobIntentionList.find(item => item.name == "entryTime").baseText =
        val.entryTime;
      this.jobIntentionList.find(item => item.name == "salary").baseText =
        val.salary0 + 10 + "k ~ " + (val.salary1 + 10) + "k";
      console.log(this.jobIntentionList);
      // this.postMainDatas();
      
    },
    // 
//     postMainDatas(){
//       Bus.$emit("postMainData",{
// jobIntentionList:this.jobIntentionList
//       }
        
//       )
//     },
    // 
    addEdus(name) {
      if (name == "edu") {
        var objEdu = {
          eduDate: "",
          schooName: "",
          majorName: "",
          eduContent: ""
        };
        this.eduList.push(objEdu);
      }
      if (name == "experience") {
        var objExperience = {
          date: "",
          name: "",
          positionName: "",
          content: ""
        };
        this.experienceList.push(objExperience);
      }
    },
    // 
    getSetting() {
      Bus.$on("changeSetting", settingObj => {
        this.settingObj = settingObj;
      });
    },
     // 
     saveContent(nameId){
       console.log(this.formData, "====");
      //  iddom
        let doms = document.getElementById("code")
       this.common.transformImage(doms).then(dataUrl=>{
      // 
      var content = {
        userName: localStorage.getItem("userName"), //
        hasCommonResume: false, //
        resumeTemplate: [
          //
          {
            TemplateId: nameId, //ID
            img:{
              url:dataUrl,
              name:nameId
            },
            resumeContent: {
              //
              baseInfoList: this.formData, //
              // SkillList: this.hasSkillList, //
              jobIntentionList: this.jobIntentionList, //
              eduList: this.eduList, //
              experienceList: this.experienceList, //
              selfEvaluation: this.selfEvaluation //
            }
          }
        ]
      };

      this.axios
        .post("resumeTemplates/resumeTemplates", { 
          content: content,
          TemplateId:nameId
           })
        .then(res => {
          if (res.data.status == "0") {
            // this.$Message.success("");
            Bus.$emit("showMoreResume")
          }
        })
        .catch(err => {
          console.log("err", err);
        });
      })

     },
    //  IDE
     saveContentss(){
        Bus.$on("saveContents", () => {
          this.saveContent(this.$route.query.id)
        })
       
     },
     // 
    // 
    getTemplatesResume(){
      this.axios
        .get("resumeTemplates/getTemplatesResume", {
          params: {
         userName: localStorage.getItem("userName"), //vuex
         TemplateId:this.$route.query.id
        }
        })
        .then(res => {
          if (res.data.status == "0") {
            this.modal_loading = false;
            this.modal2 = false;
           var result = res.data.result;
          //  null.xxx
           if(!result){
             return
           }
           this.getTemplatesResumes(result)
          }
        })
        .catch(err => {});
    },
     // 
    getTemplatesResumes(result){
      var content = result.resumeContent;
      if (content.baseInfoList.headPic == "") {
        this.showHeadImg = true;
      } else if (content.baseInfoList.headPic == "") {
        this.showHeadImg = false;
      }

      console.log(content, "====");
      // 
      this.baseObjC = {
        name: content.baseInfoList.name,
        birthday: content.baseInfoList.birthday,
        age: content.baseInfoList.age, //
        tel: content.baseInfoList.tel,
        mail: content.baseInfoList.mail,
        work: content.baseInfoList.work, //
        headPic: content.baseInfoList.headPic, //
        wordDescribe: content.baseInfoList.wordDescribe, //
        showDescribe: content.baseInfoList.showDescribe //
      };
      // 
      let baseInfoList = [
        //
        {
          type: "ios-contact",
          baseText: "",
          inputText: content.baseInfoList.age || ""
        },
        {
          type: "md-briefcase",
          baseText: "",
          inputText: content.baseInfoList.work || ""
        },
        {
          type: "ios-call",
          baseText: "",
          inputText: content.baseInfoList.tel || ""
        },
        {
          type: "ios-mail",
          baseText: "",
          inputText: content.baseInfoList.mail || ""
        }
      ]
      // console.log(this.baseInfoList,"this.baseInfoList")
        Bus.$emit("getFormData",[content.baseInfoList,baseInfoList,this.baseObjC])
        // (this.formData = content.baseInfoList);
      this.hasSkillList = content.SkillList;
      this.jobIntentionList = content.jobIntentionList;
      this.eduList = content.eduList;
      this.experienceList = content.experienceList;
      this.selfEvaluation = content.selfEvaluation;
 
    },
    // 
      findHasResume() {
      this.axios
        .get("resumeTemplates/findHasResume", {
          params: {
            userName: localStorage.getItem("userName"), //vuex
            TemplateId: this.$route.query.id
          }
        })
        .then(res => {
          if (res.data.status == "0") {
            this.modal2 = true;
          }
        })
        .catch(err => {
          this.modal2 = false;
        });
    }
  },
  mounted(){
    // 
    // 
    if(this.showFind==="1"){
      this.getTemplatesResume()
    }else{
      this.findHasResume()
    }
        this.findHasResume()
        Bus.$on("showFindModal",data=>{
      this.modal2 = false
    })
   
    Bus.$on("getResumes",()=>{
      // 
    this.getTemplatesResume();
    })
    // 
    // this.getTemplatesResume();
        // 
   this.saveThemes();
   this.getSetting();
   this.saveContentss();
  //  
     Bus.$on("saveRecommend",(id)=>{
     if(id==4){
       this.saveContent(4)
     }
   })
  },
   // $on
  beforeDestroy(){
    Bus.$off("saveContentss")
  }
};
</script>
<style lang="less" scoped> 
 
// 
.jobList {
  text-align: center;
  width: 25%;
  display: inline-block;
  padding: 20px 5px;
  .jobIcon {
    color: #747474;
    margin-top: -2.5px;
  }
  .jobText {
    color: #747474;
    font-size: 14px;
  }
}
// .jobList:last-child{
// margin-right: 0;
// }

// .border-none{
//       /deep/ .ivu-input{
//         border: none;
//     }
// /depp/ .ivu-input:focus{
//         box-shadow:none;
//         background: #efeeee;
//     } 
// }
.jm-edu-col {
  margin-top: 10px;
}
.input-introduce {
//   width: calc(100% - 20px);
//   padding:0 10px;
  margin-bottom: 20px;
  // textarea
  /deep/ textarea {
    resize: none;
  }
}
.edu-row{
    padding: 0 10px;
    /deep/ .ivu-input:focus{
        box-shadow:none;
        background: #eaeaea;
        border: none;
    } 
    // input
  /deep/ .ivu-input{
        border: none;
    }
}
</style>