<template>
  <div>
    <div class="layout">
      <Layout>
        <!-- <Header class="zm-header"> -->
        <NavHeader></NavHeader>
        <!-- </Header> -->
        <Content class="zm">
          <div class="zm-content">
            <p> {{ current + 1 }} </p>
            <Steps :current="current">
              <Step title=""></Step>
              <Step title=""></Step>
              <Step title=""></Step>
              <Step title=""></Step>
              <Step title=""></Step>
              <Step title=""></Step>
            </Steps>
            {{ current }}
            <!--  -->
            <div class="zm-table-content" v-if="step[0]">
              <Form
                ref="formValidate"
                :model="formValidate"
                :rules="ruleValidate"
                :label-width="80"
              >
                <FormItem label="" prop="name">
                  <Input
                    v-model="formValidate.name"
                    placeholder=""
                  ></Input>
                </FormItem>
                <FormItem label="" prop="tel">
                  <Input
                    v-model="formValidate.tel"
                    placeholder=""
                  ></Input>
                </FormItem>
                <FormItem label="Email" prop="email">
                  <Input
                    v-model="formValidate.email"
                    placeholder=""
                  ></Input>
                </FormItem>

                <FormItem label="" prop="education">
                  <Select
                    v-model="formValidate.education"
                    placeholder=""
                  >
                    <Option value="beijing"></Option>
                    <Option value="shanghai"></Option>
                    <Option value="shenzhen"></Option>
                    <Option value="shenzhen"></Option>
                    <Option value="shenzhen"></Option>
                    <Option value="shenzhen"></Option>
                    <Option value="shenzhen"></Option>
                  </Select>
                </FormItem>
                <FormItem label="" prop="address">
                  <Input
                    v-model="formValidate.address"
                    placeholder=""
                  ></Input>
                </FormItem>
                <FormItem label="" prop="salary">
                  <Input
                    v-model="formValidate.salary"
                    placeholder="13k"
                  ></Input>
                </FormItem>
                <FormItem label="" prop="position">
                  <Input
                    v-model="formValidate.position"
                    placeholder="web"
                  ></Input>
                </FormItem>

                <!--  -->
              </Form>
           
              
              
              <!-- <Row>
                <Col span="2"><span class="zm-table-tip"></span></Col>
                <Col span="8"><Input v-model="name"></Input></Col>
              </Row>
              <Row>
                <Col span="2"><span class="zm-table-tip"></span></Col>
                <Col span="8"
                  ><Input
                    v-model="tel"
                    ninput="value=value.replace(/[^\d]/g,'')"
                  ></Input
                ></Col>
              </Row>
              <Row>
                <Col span="2"><span class="zm-table-tip">Email</span></Col>
                <Col span="8"><Input v-model="email"></Input></Col>
              </Row>
              <Row>
                <Col span="2"><span class="zm-table-tip"></span></Col>
                <Col span="8"><Input v-model="education"></Input></Col>
              </Row>
              <Row>
                <Col span="2"><span class="zm-table-tip"></span></Col>
                <Col span="8"><Input v-model="address"></Input></Col>
              </Row>
              <Row>
                <Col span="2"><span class="zm-table-tip"></span></Col>
                <Col span="8"><Input v-model="salary"></Input></Col>
              </Row> -->
            </div>
            <!--  -->
            <div class="zm-table-content" v-if="step[1]">
              <Row v-for="(item, index) in skillsList" :key="index">
                <Col span="2"
                  ><span class="zm-table-tip">{{ item.num }}</span></Col
                >
                <Col span="8"
                  ><Input
                    :placeholder="item.tip"
                    v-model="item.InputModal"
                  ></Input
                ></Col>
                <Col span="2" v-if="item.num > 4"
                  ><Button @click="reduceSkill(item.num)">-</Button></Col
                >
              </Row>

              <Row>
                <Col span="2"
                  ><span class="zm-table-tip"
                    ><Button type="info" @click="addSkill">+</Button></span
                  ></Col
                >
              </Row>
              <div></div>
            </div>
            <!--  -->
            <div class="zm-table-content" v-if="step[2]">
              <Row v-for="(item, index) in eduList" :key="index">
                <Col span="2"
                  ><span class="zm-table-tip">{{ item.num }}</span></Col
                >
                <Col span="8"
                  ><Input
                    :placeholder="item.tip"
                    v-model="item.InputTime"
                  ></Input
                ></Col>
                <Col span="8"
                  ><Input
                    :placeholder="item.tip"
                    v-model="item.InputModal"
                  ></Input
                ></Col>
                <Col span="2" v-if="item.num > 4"
                  ><Button @click="reduceEdu(item.num)">-</Button></Col
                >
              </Row>

              <Row>
                <Col span="2"
                  ><span class="zm-table-tip"
                    ><Button type="info" @click="addEdu">+</Button></span
                  ></Col
                >
              </Row>
              <div></div>
            </div>
            <!--  -->
            <div class="zm-table-content" v-if="step[3]">
              <div v-for="(item, index) in workList" :key="index">
                <Row>
                  <Col span="12">
                    <span class="zm-table-tip">{{ item.companyName }}</span>
                    <Input
                      :placeholder="item.companyTip"
                      v-model="item.companyModel"
                    ></Input>
                  </Col>
                  <Col span="12">
                    <span class="zm-table-tip"></span>
                    <Input
                      :placeholder="item.timeTip"
                      v-model="item.timeModel"
                    ></Input>
                  </Col>
                </Row>
                <Row>
                  <Col span="12">
                    <span class="zm-table-tip">{{ item.projectName }}</span>
                    <Input
                      v-model="item.projectModel"
                      :placeholder="item.projectTip"
                    ></Input>
                  </Col>
                  <Col span="12">
                    <span class="zm-table-tip">{{ item.tech }}</span>
                    <Input
                      v-model="item.techModel"
                      :placeholder="item.techTip"
                    ></Input>
                  </Col>
                </Row>
                <span class="zm-table-tip">{{ item.work }}</span>
                <Textarea
                  :placeholder="item.workTip"
                  v-model="item.workModel"
                ></Textarea>
              </div>
            </div>
            <!--  -->
            <div class="zm-table-content" v-if="step[4]">
              <Row v-for="(item, index) in projectList" :key="index">
                <Col span="2"
                  ><span class="zm-table-tip">{{ item.num }}</span></Col
                >
                  <Col span="8"
                  ><Input
                    :placeholder="item.tip"
                    v-model="item.InputName"
                  ></Input
                ></Col>
                <Col span="8"
                  ><Input
                    :placeholder="item.tip"
                    v-model="item.InputModal"
                  ></Input
                ></Col>
                <Col span="2" v-if="item.num > 4"
                  ><Button @click="reduceEdu(item.num)">-</Button></Col
                >
              </Row>

              <Row>
                <Col span="2"
                  ><span class="zm-table-tip"
                    ><Button type="info" @click="addEdu">+</Button></span
                  ></Col
                >
              </Row>
              <div></div>
            </div>
            <div class="zm-table-content" v-if="step[5]">
             <Row v-for="(item, index) in evaluateList" :key="index">
                <Col span="2"
                  ><span class="zm-table-tip">{{ item.num }}</span></Col
                >
                
                <Col span="8"
                  ><Input
                    :placeholder="item.tip"
                    v-model="item.InputModal"
                  ></Input
                ></Col>
                <Col span="2" v-if="item.num > 4"
                  ><Button @click="reduceEdu(item.num)">-</Button></Col
                >
              </Row>

              <Row>
                <Col span="2"
                  ><span class="zm-table-tip"
                    ><Button type="info" @click="addEdu">+</Button></span
                  ></Col
                >
              </Row>
              <div></div>
            </div>
           <div style="margin-top:20px;">
                  <Button
                    class="zm-next-btn-pre"
                    type="primary"
                    @click="pre"
                    v-if="current > 0"
                    ></Button
                  >
                  <Button
                    @click="handleReset('formValidate')"
                    style="margin-left: 8px"  v-if="current == 5"
                    ></Button
                  >

                  <Button
                    class="zm-next-btn-next"
                    type="primary"
                    @click="next"
                    v-if="current < 5"
                    ></Button
                  >
                  <Button
                    class="zm-next-btn-next"
                    type="primary"
                    @click="complete"
                    v-if="current == 5"
                    ></Button
                  >
            </div>
          </div>
        </Content>
        <Footer>
          <!-- <NavFooter></NavFooter> -->
        </Footer>
      </Layout>
    </div>
  </div>
</template>

<script>
import NavHeader from "@/components/NavHeader";
import NavFooter from "@/components/NavFooter";

export default {
  name: "WriteBaseInfo",
  data() {
    return {
      current: 0,
      step: [true, false, false, false, false, false],
      // 
      skillsList: [
        {
          num: 1,
          tip: "Web Javascript()/xxx/xxxx/xxxxxx/xxxxx",
          InputModal: ""
        },
        {
          num: 2,
          tip: " HTML(5)/xxxxx/xxxxxx/xxxxxx/xxxxx/xxxxxxx",
          InputModal: ""
        },
        { num: 3, tip: " MySQL/xxxx", InputModal: "" },
        { num: 4, tip: " Linux/xxxxx", InputModal: "" }
      ],
      // 
      eduList: [
        {
          num: 1,
          tip: " ",
          InputTime: "",
          InputModal: ""
        },
        {
          num: 2,
          tip: " ",
            InputTime: "",
          InputModal: ""
        },
        { num: 3, tip: "",   InputTime: "",InputModal: "" },
        { num: 4, tip: " ",   InputTime: "",InputModal: "" }
      ],
     // 
      projectList: [
        {
          num: 1,
          tip: "",
          InputName: "",
          InputModal: ""
        },
        {
          num: 2,
         tip: "",
            InputName: "",
          InputModal: ""
        },
        { num: 3,  tip: "",   InputName: "",InputModal: "" },
        { num: 4,  tip: "",   InputName: "",InputModal: "" }
      ],
     // 
      evaluateList: [
        {
          num: 1,
          tip: "",
          InputModal: ""
        },
        {
          num: 2,
         tip: "",
          InputModal: ""
        },
        { num: 3,  tip: "",  InputModal: "" },
        { num: 4,  tip: "", InputModal: "" }
      ],
      // 
      workList: [
        {
          // 
          companyName: "",
          companyTip: "",
          companyModel: "",
          // 
          time: "",
          timeTip: "2019/07-2019/08",
          timeModel: "",
          // 
          projectName: "",
          projectTip: "",
          projectModel: "",
          // 
          tech: "",
          techTip: "js,css",
          techModel: "",
          // 
          work: "",
          workTip: "2019/07-2019/08",
          workModel: ""
        },
        {
          // 
          companyName: "",
          companyTip: "",
          companyModel: "",
          // 
          time: "",
          timeTip: "2019/07-2019/08",
          timeModel: "",
          // 
          projectName: "",
          projectTip: "",
          projectModel: "",
          // 
          tech: "",
          techTip: "js,css",
          techModel: "",
          // 
          work: "",
          workTip: "2019/07-2019/08",
          workModel: ""
        }
      ],
      skillNum: 4, //,
      EduNum: 4, //,

      baseFlag: false, //
      // 
      formValidate: {
        // 
        name: "",
        tel: "",
        email: "",
        education: "",
        address: "",
        // salary:"",
        // position:"",
        // gender: "",
        // interest: [],
        // date: "",
        // time: "",
        // desc: ""
      },
      // 
      ruleValidate: {
        name: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        tel: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        email: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          { type: "email", message: "Incorrect email format", trigger: "blur" }
        ],
        education: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        address: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        // salary: [
        //   {
        //     required: true,
        //     message: "",
        //     trigger: "change"
        //   }
        // ],
        // position: [
        //   {
        //     required: true,
        //     message: "",
        //     trigger: "change"
        //   }
        // ],
        // gender: [
        //   { required: true, message: "Please select gender", trigger: "change" }
        // ],
        // interest: [
        //   {
        //     required: true,
        //     type: "array",
        //     min: 1,
        //     message: "Choose at least one hobby",
        //     trigger: "change"
        //   },
        //   {
        //     type: "array",
        //     max: 2,
        //     message: "Choose two hobbies at best",
        //     trigger: "change"
        //   }
        // ],
        // date: [
        //   {
        //     required: true,
        //     type: "date",
        //     message: "Please select the date",
        //     trigger: "change"
        //   }
        // ],
        // time: [
        //   {
        //     required: true,
        //     type: "string",
        //     message: "Please select time",
        //     trigger: "change"
        //   }
        // ],
        // desc: [
        //   {
        //     required: true,
        //     message: "Please enter a personal introduction",
        //     trigger: "blur"
        //   },
        //   {
        //     type: "string",
        //     min: 20,
        //     message: "Introduce no less than 20 words",
        //     trigger: "blur"
        //   }
        // ]
      }
      // name: "", //
      // tel: "", //
      // email: "", //email
      // education: "", //
      // address: "", //
      // salary: "" //
      // skillsListInputModal
      // eduListInputModal
    };
  },
  components: {
    NavHeader,
    NavFooter
  },
  watch: {
    current(newV, oldV) {
      this.step = [false, false, false, false, false, false];
      this.step[newV] = true;
    }
  },
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.baseFlag = true;
          // this.$Message.success("Success!");
        } else {
          this.baseFlag = false;
          this.$Message.error("!");
        }
      });
    },
    handleReset(name) {
      this.$refs[name].resetFields();
    },
    // 
    complete() {
      // 
      //
      var base =[
              { "name": this.formValidate.name},
              { "tel": this.formValidate.tel},
              { "Email": this.formValidate.email},
              { "edu": this.formValidate.education},
              { "address": this.formValidate.address},
              this.formValidate.salary?{ "salary": this.formValidate.salary}:"",
              this.formValidate.position?{ "position": this.formValidate.position}:"",
            ];
       //
      var major = [];
      this.skillsList.forEach((item,index)=>{
        if(item.InputModal){
major.push(item.InputModal)
        }
        
      }) ;
      // 
      var education=[];
       this.eduList.forEach((item,index)=>{
         if(item.InputTime && item.InputModal){
 var educationObj={};  
        educationObj.time = item.InputTime
        educationObj.type = item.InputModal
        education.push(educationObj)
         }
        
      }) ;
       //
       var work=[];
       this.workList.forEach((item,index)=>{
         if(item.companyModel && item.timeModel && item.techModel && item.projectName && item.projectModel){

         
         var workObj={};  
         workObj.company=item.companyModel
         workObj.time=item.timeModel
         workObj.technologyStack=item.techModel
         workObj.projectName=item.projectName
         workObj.content=item.projectModel
        work.push(workObj)
         }
      }) ;
      //
      var project=[];
         this.projectList.forEach((item,index)=>{
           if(item.InputName && item.InputModal){
              var projectObj={};  
        projectObj.projectName = item.InputName
        projectObj.content = item.InputModal
        project.push(projectObj)
           }
        
      }) ;
      //
      var evaluate =[];
        this.evaluateList.forEach((item,index)=>{
          if(item.InputModal){
 evaluate.push(item.InputModal)
          }
       
      }) ;


      this.axios
        .post("resumes/resumeInfo", {
          userName: "long", //vuex
          hasCommonResume:true,//
          content: {
           base,
            major,
            work, 
            project,
            education,
            evaluate
          }
        })
        .then(res => {
          if (res.data.status == "0") {
            this.$Message.success("");
            this.$router.push({
              name: "resumeTemplate1"
            });
          }
        })
        .catch(err => {
          this.$Message.error("");
        });
    },
    next() {
      // 
      if(this.current==0){
        this.handleSubmit("formValidate");
      }
      
      if (!this.baseFlag) {
        return false;
      } else {
        if (this.current == 5) {
          this.current = 0;
        } else {
          this.current += 1;
        }
      }
    },
    pre() {
      if (this.current == 0) {
        this.current = 5;
      } else {
        this.current -= 1;
      }
    },
    // 
    addSkill() {
      this.skillNum++;
      this.skillsList.push({
        num: this.skillNum,
        tip: "Web Javascript()/xxx/xxxx/xxxxxx/xxxxx",
        InputModal: ""
      });
    },
    // 
    addEdu() {
      this.EduNum++;
      this.eduList.push({
        num: this.EduNum,
        tip: " ",
        InputModal: ""
      });
    },
    //
    reduceSkill(val) {
      this.skillsList.map((item, index) => {
        if (val == item.num) {
          this.skillsList.splice(index, 1);
        }
      });
      if (this.skillsList.length == 4) {
        this.skillNum = 4;
      }
    },
    //
    reduceEdu(val) {
      this.eduList.map((item, index) => {
        if (val == item.num) {
          this.eduList.splice(index, 1);
        }
      });
      if (this.eduList.length == 4) {
        this.EduNum = 4;
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.ivu-layout {
  background: #fff;
}
.ivu-layout-header {
  background: #fff;
}
.zm {
  margin-left: 100px;
  margin-right: 100px;
  .zm-content {
    width: 1000px;
    margin-left: 40px;
    margin-right: 40px;
    .zm-next-btn-next {
      float: right;
    }
    .zm-next-btn-pre {
      float: left;
    }
    .zm-table-content {
      width: 800px;
      margin-left: 100px;
      margin-right: 100px;
      margin-top: 40px;
      // text-align: center;
      .zm-table-tip {
        height: 32px;
        line-height: 32px;
      }
    }
  }
}
</style>
