<template>
  <div class="jm-sider">
    <ul class="jm-ul">
      <li class="jm-li">
        <div class="jm-text"></div>
        <div class="jm-msg">,</div>
        <Button @click="showCase" class="jm-btn" type="info" icon="ios-search"
          ></Button
        >
        <div class="jm-line"></div>
      </li>
      <li class="jm-li">
        <div class="jm-text"></div>
        <div class="jm-msg">,</div>
        <Button class="jm-btn" type="info" icon="ios-search"></Button>
        <div class="jm-line"></div>
      </li>
    </ul>
    <!-- v-if -->
    <div v-if="value1">
      <Drawer title="" :closable="false" v-model="value1">
        <div>
          <!-- showFind="1" -->
          <TemplateNames showFind="1"></TemplateNames>
        </div>
      </Drawer>
    </div>
  </div>
</template>

<script>
// urlid
var urlPath = window.location.href;
if (urlPath.indexOf("?") != -1) {
  var str = urlPath.substr(1);
  var strs = str.split("=");
  var templateId = strs[1];
  console.log(templateId, "templateId");
}
// webpack
// idid
var templateName;
//
const path = require("path");
// 3,
const files = require.context("./../WriteResumeTemplate", false, /\.vue$/);
// console.log(files,"")
// Object.keys() 
const modules = {};
files.keys().forEach(key => {
  const name = path.basename(key, ".vue");
  if (name == "Template" + templateId) {
    templateName = name;
    modules[templateName] = files(key).default || files(key);
    console.log(modules);
  }
});
export default {
  name: "WriteIdeSiderRight",
  data() {
    return {
      value1: false
    };
  },
  components: { TemplateNames: modules[templateName] },
  watch: {},
  methods: {
    showCase() {
      this.value1 = true;
    }
  },
  mounted() {},
  created() {}
};
</script>

