.jm-sider {
  // position: fixed;
  // width: 70px;
  background: #00c091;
  padding-right: 100px;
  margin-top: 30px;
  .jm-ul {
    .jm-li {
      background: #fff;
      text-align: center;
      .jm-text {
        // margin-top:10px;
        font-size: 18px;
        color: #525867;
        padding-top: 10px;
      }
      .jm-msg {
        padding: 0 20px;
        color: #a4aab9;
        font-size: 14px;
      }
      .jm-btn {
        margin-top: 10px;
        margin-bottom: 10px;
        font-size: 16px;
        font-weight: bold;
      }
      .jm-line {
        background: #00c091;
        height: 5px;
      }
    }
  }
}
