<template>
  <!--  -->
  <div
    v-if="showHeadImg"
    @mouseenter="enter('headImg')"
    @mouseleave="leave('headImg')"
    class="jm-headImg"
    :class="isBaseLine == 'headImg' ? 'jm-headImg-lineIs' : ''"
  >
    <Icon
      v-show="isBaseLine == 'headImg'"
      class="jm-head-icon"
      size="20"
      type="md-settings"
      @click="displayModelBase('headImg')"
    />
    <div class="head-icon" :style="imgFartherClass">
      <jmUploadImg
        :ImgBase64="formData.ImgBase64"
        :imgClass="imgClass"
        :isHeadImg="isBaseLine == 'headImg'"
      />
    </div>
    <!--  -->
    <div class="setting-head" @mouseleave="hiddenSetting">
      <setting3 v-if="showSetting">
        <div slot="content">
          <div style="margin-bottom:10px;">
            <span style="float:left;margin-left:15px;"></span>
            <span style="margin-left:40px;"
              >
              <i-Switch
                size="large"
                v-model="headIconHidden"
                @on-change="changeSwitch(-1, headIconHidden)"
              >
                <span slot="open"></span>
                <span slot="close"></span>
              </i-Switch>
            </span>
          </div>

          <div
            class="head-img-example"
            @mouseenter="bgHeadIconIndex = index"
            @mouseleave="bgHeadIconIndex = -1"
            v-for="(item, index) in headIconList"
            :key="index"
            :class="bgHeadIconIndex == index ? 'bg-head-icon' : ''"
            @click="changeHeadIcon(index)"
          >
            <img
              class="round-head-icon"
              :class="item.otherClass"
              :src="item.imgSrc"
            />
            <div>{{ item.text }}</div>
          </div>
        </div>
      </setting3>
    </div>
  </div>
</template>
<script>
import jmUploadImg from "@/components/UploadImg";
import setting3 from "@/views/resume/WriteResumeTemplate/Template3/setting3";
import Bus from "@/assets/event-bus.js";
export default {
  name: "headContent",
  props: ["formData","showHeadImg"],
  components: {
    jmUploadImg,
    setting3
  },
  data() {
    return {
      isBaseLine: "", //baseLine
      isHeadImg: false, //
      showSetting: false, //
      headIconHidden: true, //
      bgHeadIconIndex: "", //
      imgFartherClass: "", //dom
      imgClass: "", //
      headIconList: [
        {
          otherClass: "",
          imgSrc: "./../../static/image/1.jpg",
          text: ""
        },
        {
          otherClass: "one-head-icon",
          imgSrc: "./../../static/image/1.jpg",
          text: "11"
        },
        {
          otherClass: "three-head-icon",
          imgSrc: "./../../static/image/1.jpg",
          text: "34"
        }
      ]
    };
  },
  methods: {
    // 
    hiddenSetting() {
      this.showSetting = false;
    },
    // 
    changeHeadIcon(index) {
      if (index == 0) {
        console.log(index, "099009");
        this.imgClass = "round-head";
        this.imgFartherClass = "height:128px;";
      } else if (index == 1) {
        this.imgClass = "one-head";
        this.imgFartherClass = "height:128px;";
      } else if (index == 2) {
        this.imgClass = "three-head";
        this.imgFartherClass = "height:156px;";
      }
      this.showSetting = false;
    },
    // 
    changeSwitch(index, value) {
        console.log(value,"value")
      if (index == -1) {
        // this.showHeadImg = value;
        var abc;
        value ? (abc = "") : (abc = "");
        Bus.$emit("getShowHeadImg", abc);
      }
    },
    // 
    displayModelBase(name) {
      this.showSetting = true;
    },
    // 
    enter(name) {
      this.isBaseLine = name;
    },
    //
    leave(name) {
      this.isBaseLine = "";
    }
  },
  mounted(){
      this.headIconHidden = this.showHeadImg
  }
};
</script>
<style lang="less" scoped>
// 
.jm-headImg {
  position: relative;
  display: block;
  width: 170px;
  margin: 0 auto;
  padding: 20px;
  border: 1px dashed transparent !important;
  .head-icon {
    position: relative;
    width: 128px;
    height: 156px;
    margin: 0 auto;
    border: 4px solid #e5e5e5;
    background: #e5e5e5;
    overflow: hidden;
    cursor: pointer;
  }
}

/deep/.ivu-input {
  border: none !important;
}
// 
.jm-headImg-lineIs {
  border: 1px dashed #00c091 !important;
}
.jm-head-icon {
  position: absolute;
  top: 0;
  right: 0;
  color: #fff;
  background: #00c091;
  margin: 5px;
  z-index: 1;
  cursor: pointer;
}
// 
// 
.round-head-icon {
  width: 40px;
  height: 40px;
  border-radius: 20px;
}
// 1:1
.one-head-icon {
  border-radius: 0;
}
// 1:1
.three-head-icon {
  width: 30px;
  height: 40px;
  border-radius: 0;
}
.head-img-example {
  width: 52px;
  height: 79px;
  display: inline-block;
  margin: 5px;
  padding: 5px;
  border: 1px solid rgb(148, 147, 147);
  border-radius: 5px;
  cursor: pointer;
}
.setting-head {
  position: absolute;
  z-index: 10;
  top: 20px;
  padding-top: 20px;
  right: -10px;
}
.bg-head-icon {
  background: #00c091;
}
</style>

