<template>
  <div>

    <Layout class="layout zm">
      <!--  -->
      <Sider hide-trigger class="zm-left-side">Sider</Sider>
      <!--  -->
      <Layout class="zm-center">
        <Header class="zm-right-header">
          <div class="zm-top">
            <div class="zm-icon">
              <img src="static/image/user1.jpg" />
            </div>
            <div class="zm-user">
              <div></div>
              <div>online</div>
            </div>
          </div>

        </Header>
        <!--  -->
        <Content class="zm-right-content">
          <div class="zm-top"></div>
          <ul class="zm-msg">
            <li
              v-for="(item,index) in msgList"
              :key="index"
              :class="item.round=='left'?'zm-msg-detail':'right'"
            >
              <img
                :src="item.img"
                class="zm-msg-log"
              />
              <div class="zm-log-user">
              <span>{{item.name}}</span>
              <span class="zm-log-time">2019/08/13 22:56</span>
               </div>
              <div :class="item.round=='left'?'zm-left-bg':'zm-right-bg'">
                {{item.msg}}
                </div>
          
            </li>
            <!--  -->
          <div
            class='system'
            v-if="systemNews"
          >
            <div>{{chartDeta}}</div>
            <div>{{chartName}} {{chartStatus}}</div>
          </div>
          </ul>
          
         
           <Footer class="zm-footer">
          <div>
            <Input
              v-model="inputMsg"
              placeholder="Enter something..."
              class="zm-input" 
            />
            <Icon
              size="35"
              type="ios-send" @click="postMsg"
            />
          </div>
        </Footer>
        </Content>
        <!--  -->
      
      </Layout>
         <!--  -->
      <Sider hide-trigger class="zm-right-side">
         <!--  -->
          <div class="zm-group-content">
            <div class="zm-grounp-msg"></div>
            <ul class="zm-msg">
              <li
                v-for="(item,index) in userList"
                :key="index"
                class="zm-msg-detail"
              >
                <img
                  :src="item.img"
                  class="zm-msg-log"
                />
                <span>{{item.userName}}</span>
              </li>
            </ul>
          </div>
      </Sider>
    </Layout>
  </div>
</template>

<script>
import io from "socket.io-client";
export default {
  name: "GroupChart",
  data() {
    return {
      msgLeft: true, //
      msgList: [
        // {
        //   img: "static/image/user1.jpg",
        //   msg:
        //     "",
        //   type: "left"
        // },
        // {
        //   img: "static/image/user2.jpg",
        //   msg: "",
        //   type: "right"
        // },
        // {
        //   img: "static/image/user1.jpg",
        //   msg: "",
        //   type: "left"
        // },
        // {
        //   img: "static/image/user2.jpg",
        //   msg: "",
        //   type: "right"
        // }
      ], //
      inputMsg: "", //
      userList: [
        { userName: "long", img: "static/image/user1.jpg" },
        { userName: "wei", img: "static/image/user2.jpg" }
      ],
      // 
      chartDeta: "", //
      chartStatus: "", //
      systemNews: false, //,
      chartName:"",//
    };
  },
  methods: {
    // 
    postMsg() {
      if (this.inputMsg == "") {
        alert("");
        return false;
      }

      // --> 
      this.socket.emit("sendMsg", {
        msg: this.inputMsg,

      });
      this.inputMsg = "";

      return false;
    }
  },
  // socketio
  mounted() {
    // 
    this.socket = io.connect("http://localhost:3000");
    //  
    // 
    let headIcon = Math.floor(Math.random() * 4) + 1;

    //-->  
    this.socket.emit("postUser", {
      userName: localStorage.getItem("userName"),
      img: "static/image/user" + headIcon + ".jpg"
    }); // 


  // --> 
    //
    this.socket.on("system", systemInfo => {
      console.log(systemInfo)
      this.chartDeta = new Date().toTimeString().slice(0, 9);
      this.chartStatus = systemInfo.status;
      this.chartName = systemInfo.userName;
      this.systemNews = true;
      // 
    });


    //--> 
    this.socket.on("onlineUser", usersList => {
      this.userList = usersList;
    });

    //--> 
    this.socket.on("receiveMsg", usersList => {
      this.msgList.push(usersList);
    });

  
  }
};
</script>

<style lang="less" scoped>
.zm{
  padding:20px;
  width:100%;
  height: 500px;
}
// 
.zm-left-side{
    width: 200px;
    // display: flex;
    // flex-direction: column;
}


// 
.zm-right-side{
  width: 200px;
    height: 460px;
    
    background: #fff;
  // 
  .zm-group-content {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    .zm-grounp-msg{
      height: 20px;
    }
    
    .zm-msg {
      width: 100%;
      overflow-y: scroll;
      height: 420px;
      list-style: none;
      .zm-msg-detail {
        background: #fff;
        .zm-msg-log{
          width: 30px;
          height: 30px;
          border-radius: 15px;
        }
      }
    }
  }
}


// 
.ivu-layout-header {
  background: #fff;
  line-height: 20px;
}
// 
.zm-center{
    width: calc(100% - 400px);
}
.zm-right-header {
  height: 50px;
  border-top: 1px solid #ddd;
  .zm-top {
    .zm-icon {
      display: inline-block;
      img {
        display: inline-block;
        width: 39px;
        height: 39px;
      }
    }
    .zm-user {
      display: inline-block;
    }
  }
}
// 
.zm-right-content {
  position: relative;
  // height:400px;
  .zm-top {
    text-align: center;
  }
  .zm-msg {
      // height:400px;
    list-style: none;
    position: relative;
    // margin-right: 200px;

    // 
    .zm-msg-detail {
      margin: 20px;
      margin-bottom: 40px;
       position: relative;
        // margin-right: 20px;
      .zm-msg-log {
        width: 30px;
        height: 30px;
        position: absolute;
        top:0;
      }
      .zm-log-user{
        display: inline-block;
        position: absolute;
        span{
          margin-left: 40px;
        }
        .zm-log-time{
          margin-left:10px;
        }
      }
    }
    // -------------------------
    // 
    .right {
      
      margin: 20px;
      margin-bottom: 40px;
       position: relative;
       text-align: right;
        // margin-right: 20px;
      .zm-msg-log {
        width: 30px;
        height: 30px;
        position: absolute;
        top:0;
        right:0;
      }
      .zm-log-user{
        display: inline-block;
        position: absolute;
        right:40px;
        span{
          margin-left: 40px;
        }
        .zm-log-time{
          margin-left:10px;
        }
      }
  }
    
  }
  
}

// .left{
//   background: #fff;
// }
.zm-left-bg {
  word-wrap:break-word; 
  display: inline-block;
  position: relative;
  top: 20px;
  max-width: calc(100% - 40px);
  padding: 5px;
  left: 40px;
  background: #fff;
  border-radius: 5px;
  // border-radius: 10px 0px 10px 10px;
  // color: #000;
}
.zm-right-bg {
  word-wrap:break-word; 
  display: inline-block;
  position: relative;
   max-width: calc(100% - 40px);
    top: 20px;
    padding: 5px;
    right: 40px;
    text-align: left;
   
    background: #01e777;
    border-radius: 5px;
    color: #fff;
    // border-radius: 10px 0px 10px 10px;
}
// 
.zm-footer {
  top: 420px;
  width: calc(100% - 400px);
  position: fixed;
  height: 60px;
  background: #fff;
  .zm-input {
    width: 70%;
  }
}

// 
.system {
 
  text-align: center;
}
</style>
