<template>
  <!--  -->
  <div>
    <Modal class="jm-skill-modal" v-model="modalSkills">
      <p class="jm-title" slot="header" style="text-align:left">
        <span></span>
      </p>
      <div class="jm-content">
        <!--  -->
        <Form
          class="jm-form"
          :model="formData"
          label-position="left"
          :label-width="100"
        >
          <FormItem class="jm-Row" label="">
            <Input
              v-model="formData.name"
              placeholder=""
            ></Input>
          </FormItem>
          <FormItem class="jm-Row" label="">
            <!-- iviewvaluev-model -->
            <DatePicker
              v-model="formData.birthday"
              format="yyyyMMdd"
              type="date"
              placeholder=""
            ></DatePicker>
          </FormItem>
          <FormItem class="jm-Row" label="">
            <Select v-model="formData.work" clearable placeholder="">
              <Option v-for="item in workYearList" :value="item" :key="item">{{
                item
              }}</Option>
            </Select>
          </FormItem>
          <FormItem class="jm-Row" label="">
            <Select v-model="formData.headPic">
              <Option v-for="item in headPicList" :value="item" :key="item">{{
                item
              }}</Option>
            </Select>
          </FormItem>
          <FormItem class="jm-Row" label="">
            <Input v-model="formData.tel" placeholder=""></Input>
          </FormItem>
          <FormItem class="jm-Row" label="">
            <Input v-model="formData.mail" placeholder=""></Input>
          </FormItem>
        </Form>
        <!--  -->
       
          <Row class="wordDescribe"> 
            <Row>
            <Col span="8"></Col>
            <Col span="8" offset="8"
              >
              <i-Switch class="jm-switch" @on-change="changeSwitch">
                <span slot="open"></span>
                <span slot="close"></span>
              </i-Switch>
            </Col>
            </Row>
            <Row>
              <Col>
              <Input v-model="formData.wordDescribe" placeholder=" 301"></Input>
              </Col>
            </Row>
          </Row>
     
      </div>
      <div class="jm-footer" slot="footer">
        <Button @click="save" class="jm-save-btn" size="large"></Button>
        <Button @click="cancelModel" class="jm-cancle-btn" size="large"
          ></Button
        >
      </div>
    </Modal>
  </div>
</template>
<script>
import Bus from "@/assets/event-bus.js";
export default {
  name: "baseInfoModel",
  props: ["modalSkills","baseObjC"], //
  data() {
    return {
      // 
      formData: {
        name: "",
        birthday: "",
        age: 0,//
        tel: "",
        mail: "",
        work: "",//
        headPic: "",//
        wordDescribe:"",//
        showDescribe:true,//
      },
      // 
      workYearList: ["1", "2", "3"],

      // 
      headPicList: ["", ""],

    };
  },
  watch: {
    // 
    baseObjC:{
      handler(newValue,oldValue){
        this.formData.name = this.baseObjC.name;
        this.formData.birthday = this.baseObjC.birthday;
        this.formData.age = this.baseObjC.age;
        this.formData.tel = this.baseObjC.tel;
        this.formData.mail = this.baseObjC.mail;
        this.formData.work = this.baseObjC.work;
        this.formData.mail = this.baseObjC.mail;
        this.formData.headPic = this.baseObjC.headPic;
        this.formData.wordDescribe = this.baseObjC.wordDescribe;
        this.formData.showDescribe = this.baseObjC.showDescribe;
      },
      deep:true
    }
  },
  methods: {
  getShowHeadImgs(){
Bus.$on("getShowHeadImg",value=>{
  this.formData.headPic = value
})
  },
    // 
    cancelModel() {
      this.$emit("changeSkillModel", false);
    },
    // 
    save(){
      this.cancelModel();
    //  
    this.formData.age = this.formData.birthday ? new Date().getFullYear() - this.formData.birthday.getFullYear() : 0
      this.$emit("saveBaseInfo",this.formData)
    },
    // 
    changeSwitch(status){
      status ? this.formData.showDescribe = false : this.formData.showDescribe = true
    }
  },
  mounted(){
    this.getShowHeadImgs();
  }
};
</script>
 <style lang="less" scoped>
.jm-skill-modal {
  width: 600px;
  /deep/.ivu-modal {
    width: 600px !important;
  }
  // deep
  /deep/.ivu-modal-header {
    padding: 0 !important;
  }

  // 
  .jm-title {
    width: 100%;
    height: 40px;
    line-height: 40px;
    padding-left: 20px;
    color: white;
    background-color: #00c190;
    border-radius: 4px 4px 0 0;
  }
  // 
  .jm-content {
    .jm-form {
      .jm-Row {
        display: inline-block;
        width: 48%;
      }
    }
    .wordDescribe{
      .jm-switch{
       
      }
    }
  }
  // 
  .jm-footer {
    text-align: center;
    .jm-save-btn {
      background-color: #00c190;
      color: white;
      border-radius: 4px;
      border: none;
      width: 100px;
      height: 40px;
      margin-left: 15px;
      cursor: pointer;
    }
    .jm-cancle-btn {
      background-color: #ededed;
      color: #8b8b8b;
      border-radius: 4px;
      border: none;
      width: 100px;
      height: 40px;
      margin-left: 15px;
      cursor: pointer;
    }
  }
}
</style>