<script>
// import VueMarkdown from 'vue-markdown';
// import README from './README.md';
export default {
  name: "ArticleRight",
  // props:["random"],
  data() {
    return {
      listTop: {},
      listBottom: {},
      random: [],
      linkList:[]
    };
  },
  methods: {
    // 
    gotoContent(articleIndex, types, idIndex) {
      const { href } = this.$router.resolve({
        path: "/articleContent",
        query: {
          articleIndex: articleIndex,
          types: types,
          idIndex: idIndex
        }
      });
      window.open(href, "_blank");

      // this.$router.push({
      //   path: "/articleContent",
      //   query: {
      //     articleIndex: articleIndex,
      //     types:types,
      //     idIndex:idIndex
      //   }
      // });
    },
    // 
    getData(flag) {
      this.axios
        .get(`${this.baseUrl}/sidebarAdImgs/get`, {
          params: {
            flag: flag
          }
        })
        .then(res => {
          let list = res.data.resulet;
          list.forEach((item, index) => {
            if (item.types == "top" && item.show == 1) {
              this.listTop = item;
            } else if (item.types == "buttom" && item.show == 1) {
              this.listBottom = item;
            }
          });
        })
        .catch(err => {
          console.log("err", err);
        });
    },
    // 
    getLink() {
      this.axios
        .get(`${this.baseUrl}/friendLinks/get`)
        .then(res => {
          this.linkList = res.data.resulet;
        })
        .catch(err => {
          console.log("err", err);
        });
    },
    getArticle(flag) {
      this.axios
        .get(`${this.baseUrl}/articles/get`, {
          //
          params: {
            // pageSize: this.pageSize,
            // currentPage:this.currentPage,
            userName: "longwei",
            flag: flag //all
          }
        })
        .then(res => {
          res.data.resulet.forEach((item, index) => {
            let obj = {
              idIndex: item.idIndex,
              types: item.types,
              title: item.title
            };
            this.random.push(obj);
          });
          // 6
          // this.random = this.random.splice(6, 12);
           this.random = this.random.splice(1, 3);
          this.random = this.random.sort(() => Math.random() - 0.5);
        })
        .catch(err => {
          console.log("err", err);
        });
    }
  },
  computed: {},
  mounted() {
    this.getData("all");
    this.getArticle("all");
    this.getLink();
  }
};
</script>

