<template>
  <div class="jm-header">
    <div>
      <span class="head-text">
        <router-link :to="{ name: 'home' }"> </router-link>
      </span>
      <span class="head-text" @click="$router.back(-1)">
        
      </span>
      <span class="head-text">
        
      </span>
      <span class="head-text">
        <span @click="preview"></span>
      </span>
      <span class="head-text">
        <span @click="saveContent"></span>
        <div class="tips-content">
        <Alert closable  v-if="showTip" type="success" show-icon @on-close="closeTips">
          <a @click="getMOre"></a>
        </Alert>
        </div>
        <span @click="downImg"></span>
        
      </span>
      <span class="head-text">
        <span @click="toMyCenter"></span>
      </span>
      <recommendResume :modalTips="modalTips"></recommendResume>
    </div>


  </div>
</template>

<script>
import Bus from "@/assets/event-bus.js";
import RecommendResume from "./RecommendResume";

export default {
  name: "WriteIdeHeader",
  data() {
    return {
      showTip:false,
      modalTips:false
    };
  },
  components: {
    RecommendResume
  },
  methods: {
    closeTips(){
      this.showTip= false;
    },
    getMOre(){
      console.log(3)
      this.modalTips = true
      // 
      // recommendResume
    },
    showMoreResumes(){
       Bus.$on("showMoreResume",()=>{
       this.showTip = true
      });
    },
    // 
    saveContent() {
      Bus.$emit("saveContents");
    },
    // 
    preview() {
      Bus.$emit("previews");
    },
    // 
    toMyCenter() {
      this.$router.push({ name: "myResume" });
    },

    // 
    downImg() {
      this.axios
        .post("resumes/downImg", {
          name: "1",
          headers: { "content-type": "multipart/form-data" }
        })
        .then(res => {
          if (res.data.status == "0") {
            // 
            var a = document.createElement("a");
            a.download = "1";
            // 
            a.href = res.data.msg;
            a.click();
          }
        })
        .catch(err => {});
    }
  },
  watch: {},

  mounted() {
    this.showMoreResumes();
     Bus.$on("closeModel",(data)=>{
       this.modalTips = data
     })
   
    
  },
  created() {}
};
</script>

<style lang="less" scoped>
.jm-header {
  height: 50px;
  text-align: center;
  padding-right: 50px;
  .head-text {
    padding-right: 100px;
    cursor: pointer;
  }

  /deep/.ivu-layout-header {
    height: 50px;
    line-height: 50px;
  }
}
.tips-content{
    position: absolute;
     left:685px;
     top: 45px;

  }
  .tips-content::after{
    display: block;
    content: "";
    height: 0;
    width:0;
    visibility: hidden;
    clear: both;
  }


</style>
