<template>
  <div class="jm-template" id="code">
    <!--  -->
    <div class="resume-top">
      <!--  -->
      <div class="resume-head">
        <headContent
          :formData="formData"
          :showHeadImg="showHeadImg"
        ></headContent>
      </div>
      <!--  -->
      <div class="resume-name">
        <div class="jm-base-name">
          <div
            style="position: relative;"
            class="jm-defult-line"
            :class="isBaseLine == 'name' ? 'jm-base-linehover0' : ''"
            @mouseenter="enterBase('name')"
            @mouseleave="leaveBase('name')"
          >
            <Icon
              v-show="isBaseLine == 'name'"
              class="jm-head-icon"
              size="20"
              type="md-settings"
              @click="displayModelBase('name')"
            />
            <div class="jm-name">
              <Input
                v-model="formData.name"
                placeholder=""
                clearable
                class="right-name"
              />
            </div>
            <div v-if="formData.showDescribe" class="jm-name-introduce">
              <Input
                v-model="formData.wordDescribe"
                placeholder="HR"
                clearable
                autosize
                type="textarea"
                class="input-introduce"
              />
            </div>
          </div>
        </div>
      </div>
      <!--  -->
      <div class="resume-name">
        <div class="jm-base-name">
          <span
            @mouseenter="enterBase('age')"
            @mouseleave="leaveBase('age')"
            class="jm-age-content jm-defult-line"
            :class="isBaseLine == 'age' ? 'jm-base-linehover0' : ''"
          >
            <!--  -->
            <Icon
              class="jm-head-icon"
              @click="displayModelBase('name')"
              v-show="isBaseLine == 'age'"
              size="20"
              type="md-create"
            />
            <div>
              <div class="age-row">
                <span class="age-left"> </span>
                <span class="age-right"> </span>
              </div>
              <div class="age-row float-r">
                <span class="age-left"> </span>
                <span class="age-right"> </span>
              </div>
            </div>

            <div>
              <div class="age-row">
                <span class="age-left"> </span>
                <span class="age-right"> </span>
              </div>
              <div class="age-row float-r">
                <span class="age-left"> </span>
                <span class="age-right"> </span>
              </div>
            </div>
          </span>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="resume-bottom">
      <div class="jobIntention">
        <div class="jobIntention-text"></div>
        <div class="jobIntention-content">
          <jobIntention
            :themeList="jobThemeList"
            themeFlag="jobIntention"
            key="1"
            :jobIntentionLists="jobIntentionList"
            name="jobIntention"
            v-on:savaJobIntention="savaJobIntention"
            title=""
          >
            <div slot="slotRight">
              <Row
                class="jobList"
                v-for="(item, index) in jobIntentionList"
                :key="index"
              >
                <Icon :size="20" class="jobIcon" :type="item.type" />
                <span class="jobText">{{ item.baseText }}</span>
              </Row>
            </div>
          </jobIntention>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import jmUploadImg from "@/components/UploadImg";
import headContent from "@/components/HeadContent";
import baseInfoModel from "./BaseInfoModel";
import Bus from "@/assets/event-bus.js";
import mainContent from "./common/MainContent";
import jobIntention from "./common/JobIntention";
export default {
  name: "Template4",
  components: {
    jmUploadImg,
    baseInfoModel,
    headContent,
    mainContent,
    jobIntention
  },
  data() {
    return {
      jobThemeList: {}, //
      eduThemeList: {}, //
      workThemeList: {}, //
      ThemeAppraiseList: {}, //
      jobIntentionList: [
        //
        {
          type: "ios-contact",
          baseText: "",
          name: "choseJob"
        },
        {
          type: "md-briefcase",
          baseText: "",
          name: "city"
        },
        {
          type: "ios-mail",
          baseText: "",
          name: "entryTime"
        },
        {
          type: "ios-call",
          baseText: "",
          name: "salary"
        }
      ],
      textWork1: "",
      isBaseLine: "", //baseLine
      name: "",
      introduce: "",
      eduText: "",
      // jmDate: "jm-date", //
      eduDate: "",
      eduschool: "",
      eduMajor: "",
      workDate: "",
      workName: "",
      workMajor: "",
      workText: "",
      project: "", //
      showHeadImg: true, //
      selfEvaluation: "", //
      inFocus: false, //textarea
      // 
      modalBaseInfo: false, //
      baseInfoList: [
        //
        {
          type: "ios-contact",
          baseText: "",
          inputText: ""
        },
        {
          type: "md-briefcase",
          baseText: "",
          inputText: ""
        },
        {
          type: "ios-call",
          baseText: "",
          inputText: ""
        },
        {
          type: "ios-mail",
          baseText: "",
          inputText: ""
        }
      ],
      baseObjC: {}, //
      formData: {
        showDescribe: true //
      }
    };
  },
  props: ["showFind"],
  watch: {},
  methods: {
    // 
    savaJobIntention(val) {
      this.jobIntentionList.find(item => item.name == "choseJob").baseText =
        val.choseJob;
      this.jobIntentionList.find(item => item.name == "city").baseText =
        val.city;
      this.jobIntentionList.find(item => item.name == "entryTime").baseText =
        val.entryTime;
      this.jobIntentionList.find(item => item.name == "salary").baseText =
        val.salary0 + 10 + "k ~ " + (val.salary1 + 10) + "k";
      console.log(this.jobIntentionList);
      // this.postMainDatas();
    },
    transformImage() {
      let doms = document.getElementById("code");
      this.common.transformImage(doms).then(dataUrl => {
        this.$router.push({ name: "preview", params: { dataUrl: dataUrl } });
      });
    },
    // 
    // 
    changeSkillModel(data) {
      this.modalBaseInfo = false;
    },
    // 
    displayModelBase(name) {
      if (name == "name") {
        this.modalBaseInfo = true;
      }
    },
    // 
    saveBaseInfo(data) {
      console.log(data, "data=====");
      for (let item in data) {
        switch (item) {
          case "birthday":
            this.baseInfoList.find(item => item.baseText == "").inputText =
              data.age + "";
            break;
          case "work":
            this.baseInfoList.find(
              item => item.baseText == ""
            ).inputText = data.work;
            break;
          case "tel":
            this.baseInfoList.find(item => item.baseText == "").inputText =
              data.tel;
            break;
          case "mail":
            this.baseInfoList.find(item => item.baseText == "").inputText =
              data.mail;
            break;
        }
        if (data.headPic == "") {
          this.showHeadImg = true;
        } else if (data.headPic == "") {
          this.showHeadImg = false;
        }
        this.formData = data;
      }
    },
    // 
    enterBase(name) {
      this.isBaseLine = name;
    },
    //
    leaveBase(name) {
      this.isBaseLine = "";
    },
    // 
    focusInput() {
      console.log(0);
      this.inFocus = true;
    },
    //
    blurInput() {
      console.log(1);
      this.inFocus = false;
    }
  },
  mounted() {
    //
    Bus.$on("getShowHeadImg", value => {
      if (value == "") {
        this.showHeadImg = true;
      } else if (value == "") {
        this.showHeadImg = false;
      }
    });
    // 
    Bus.$on("postPhotoBase64", ImgBase64 => {
      this.formData.ImgBase64 = ImgBase64;
    });

    // 
    Bus.$on("getFormData", data => {
      console.log(data, "data");
      this.formData = data[0];
      this.baseInfoList = data[1];
      this.baseObjC = data[2];
    });

    Bus.$on("previews", () => {
      this.transformImage();
    });
  }
  //  // $on
  // beforeDestroy(){
  //   Bus.$off("saveContentss")
  // }
};
</script>

<style lang="less" scoped>
.jm-template {
  height: 1160px;
  min-height: 1160px;
  background: #fff;
  position: relative;
  width: 820px;
  box-sizing: border-box;
  .resume-top {
    position: relative;
    padding: 128px 46px 25px;
    background: #fff;
    .resume-head {
      width: 174px;
      height: 200px;
      position: absolute;
      right: 36px;
      bottom: 1px;
      top: 128px;
      right: 30px;
      border: 1px solid #292929;
      margin: -1px;
      border-radius: 0;
    }
    .resume-name {
      border: 1px solid #292929;
      border-bottom: none;
      margin: -1px;
      margin-right: 174px;
      border-radius: 0;
      height: 85px;
      width: 573px;
      color: #000;
      .jm-base-name {
        position: relative;
        /deep/ .ivu-input {
          border: none;
        }
        .jm-name {
          /deep/ .ivu-input {
            font-size: 30px;
          }
          .right-name {
            width: calc(100%-45px);
            margin: 5px 40px 10px 5px;
            font-size: 30px;
          }
        }
        // 
        .jm-age-content{
          height: 112px;
          display: block;
          border-bottom: 1px solid #292929;
          border-left: 1px solid #292929;
          .age-row{
            width: 50%;
            height: 40px;
            border: 1px solid #292929;
            margin-left: -1px!important;
            margin-top: -1px;
            position: relative;
            display: inline-block;
            float: left;
            border-left: 0;
            .age-left{
              text-align: center;
              width: 84px;
              display: inline-block;
              padding-right: 8px;
              margin-right: 2px;
              line-height: 38px;
              padding-left: 5px;
              border-right:  1px solid #292929;
              border-left: none;
            }
            .age-right{
              border-right:0;
              display: inline-block;
              text-align: center;
            }
          }
          .float-r{
            float: right;
            border-right:0;
          }

        }
      }
    }
    .resume-age-content{
      border-left: none;
    }
  }
  // 
  .resume-bottom{
    margin-left: 9px;
    width: 818px;
    padding-right: 36px;
    padding-left: 36px;
    .jobIntention{
      position: relative;
      border-radius: 0;
      border:  1px solid #292929;
      background: #fff;
      color: #747474;
      height: 100%;
      .jobIntention-text{
        height: 100%;
        width: 83px;
        border-right:  1px solid #292929;
        padding: 0 16px;
        font-size: 20px;
        word-break: break-word;
        font-weight: bold;
        display: inline-block;
        text-align: justify;
        text-align-last: justify;
        vertical-align: middle;
      }
      .jobIntention-content{
        display: inline-block;
        width: 657px;
        .jobList{
          width: 25%;
          display: inline-block;
          padding: 20px 5px;
          .jobIcon{
            color:#747474;
            margin-top: -2.5px;
          }
          .jobText{
            color: #747474;
            font-size: 14px;
          }
        }
      }
    }
  }
  .resume-top::before{
    content: "";
    display: block;
    position: absolute;
    top: 40px;
    left: 0;
    right: 0;
    line-height: 1.2;
    font-size: 36px;
    text-align: center;
  }
}
.jm-base-linehover{
  border: 1px dashed #00c091;

}
.jm-head-icon{
  position: absolute;
  top: 0;
  right: 0;
  color: #fff;
  background: #00c091;
  margin: 5px;
  z-index: 1;
  cursor: pointer;
}
// /deep/ .textarea{

// }
 /deep/textarea {
  resize: none;
}
/deep/ .ivu-input:focus{
box-shadow: none;
background: #eaeaea;
border: none;
