// 
// Sun Apr 26 2020 22:05:50 GMT+0800 ()  =>  2020-04-26 22:05:50
function formatTen(num) {
  return num > 9 ? num + '' : '0' + num
}
Vue.filter('formatDate', function(date) {
  var date = new Date(date)
  var year = date.getFullYear()
  var month = date.getMonth() + 1
  var day = date.getDate()
  var hour = date.getHours()
  var minute = date.getMinutes()
  var second = date.getSeconds()
  return (
    year +
    '-' +
    formatTen(month) +
    '-' +
    formatTen(day) +
    ' ' +
    formatTen(hour) +
    ':' +
    formatTen(minute) +
    ':' +
    formatTen(second)
  )
})
// 
Vue.filter('bigTypes', function(data) {
  var datas

  switch (data) {
    case 'wx':
      datas = ''
      break
    case 'tb':
      datas = ''
      break
    case 'az':
      datas = ''
      break
    case 'ios':
      datas = ''
      break
    case 'xb':
      datas = ''
      break
    case 'gj':
      datas = ''
      break
    case 'cg':
      datas = ''
      break
  }
  return datas
})
Vue.filter('formatDateDay', function(date) {
  var date = new Date(date)
  var year = date.getFullYear()
  var month = date.getMonth() + 1
  var day = date.getDate()

  return year + '-' + formatTen(month) + '-' + formatTen(day)
})
// base
Vue.filter('formatResumeBase', function(value, fmt) {
  switch (value) {
    case 'name':
      fmt = ''
      break
    case 'tel':
      fmt = ''
      break
    case 'Email':
      fmt = ''
      break
    case 'edu':
      fmt = ''
      break
    case 'address':
      fmt = ''
      break
    case 'salary':
      fmt = ''
      break
    case 'position':
      fmt = ''
      break
    default:
      break
  }
  return fmt
})

