<template>
  <div class="jm-template">
    <Row id="code">
      <!-- {{modalSkill}} -->
      <!--  -->
      <skillModal
        :modalSkills="modalSkill"
        :hasSkillLists="hasSkillList"
        v-on:changeSkillModel="changeSkillModel"
        v-on:saveSkill="saveSkill"
      />
      <baseInfoModel
        :modalSkills="modalBaseInfo"
        :baseInfoList="baseInfoList"
        :baseObjC="baseObjC"
        v-on:changeSkillModel="changeSkillModel"
        v-on:saveBaseInfo="saveBaseInfo"
      />
      <div class="resume-left">
        <!--  -->
        <headContent
          :formData="formData"
          :showHeadImg="showHeadImg"
        ></headContent>
        <!--  -->
        <Row>
          <div
            class="jm-baseInfo"
            :class="isBase ? 'jm-headImg-lineIs' : ''"
            @mouseenter="enter('base')"
            @mouseleave="leave('base')"
          >
            <Icon
              @click="displayModelBase('base')"
              v-show="isBase"
              class="jm-head-icon"
              size="20"
              type="md-create"
            />
            <Row
              class="jm-age"
              v-for="(item, index) in baseInfoList"
              :key="index"
            >
              <Icon :type="item.type" />
              <span class="jm-baseText">{{ item.baseText }}</span>
              <span class="jm-baseText">{{ item.inputText }}</span>
            </Row>
          </div>
        </Row>
        <!--  -->
        <Row>
          <!-- mouseenterRowdiv -->
          <div
            class="jm-baseInfo"
            :class="isSkill ? 'jm-headImg-lineIs' : ''"
            @mouseenter="enter('skill')"
            @mouseleave="leave('skill')"
          >
            <Icon
              @click="displaySkillModel"
              v-show="isSkill"
              class="jm-head-icon"
              size="20"
              type="md-create"
            />
            <Row class="jm-skill-title">
              <Icon size="25" type="md-checkbox-outline" />
              <span></span>
            </Row>

            <Row class="jm-skill-content">
              <i-Circle
                :size="70"
                v-for="(item, index) in hasSkillList"
                :key="index"
                class="jm-circle"
                :percent="item.skillNumber"
              >
                <span class="demo-Circle-inner" style="font-size:24px">{{
                  item.skillNumber
                }}</span>
                <span class="jm-circle-text">{{ item.name }}</span>
              </i-Circle>
            </Row>
          </div>
        </Row>
      </div>
      <!--  -->
      <div class="resume-right" ref="rightContent">
        <!--  -->
        <Row class="jm-base-name">
          <!--  -->
          <div
            style="position: relative;"
            class="jm-defult-line"
            :class="isBaseLine ? 'jm-base-linehover0' : ''"
            @mouseenter="enter('baseRight')"
            @mouseleave="leave('baseRight')"
          >
            <Icon
              v-show="isBaseLine"
              @click="displayModelBase('base')"
              class="jm-head-icon"
              size="20"
              type="md-settings"
            />
            <div class="jm-name">
              <Input
                v-model="formData.name"
                placeholder=""
                clearable
                class="right-name"
              />
            </div>
            <div v-if="formData.showDescribe" class="jm-name-introduce">
              <Input
                v-model="formData.wordDescribe"
                placeholder="HR"
                clearable
                autosize
                type="textarea"
                class="input-introduce"
              />
            </div>
          </div>

          <!--  -->
          <Row>
            <!--  -->
            <vuedraggable class="wrapper">
              <rightContent
                :themeList="jobThemeList"
                themeFlag="jobIntention"
                key="1"
                :jobIntentionLists="jobIntentionList"
                name="jobIntention"
                v-on:savaJobIntention="savaJobIntention"
                title=""
              >
                <div slot="slotRight">
                  <Row
                    class="jobList"
                    v-for="(item, index) in jobIntentionList"
                    :key="index"
                  >
                    <Icon :size="20" class="jobIcon" :type="item.type" />
                    <span class="jobText">{{ item.baseText }}</span>
                  </Row>
                </div>
              </rightContent>
              <!--  -->
              <rightContent
                :themeList="eduThemeList"
                themeFlag="edu"
                :key="2"
                name="edu"
                v-on:addEdus="addEdus('edu')"
                title=""
              >
                <div slot="slotRight">
                  <Row v-for="(item, index) in eduList" :key="index">
                    <Row v-if="settingObj.isShowJobTime">
                      <Row>
                        <Col :span="12" class="jm-edu-col">
                          <Input
                            v-model="item.eduDate"
                            placeholder=":2016/08-2017/09"
                          />
                        </Col>
                        <Col :span="12" class="jm-edu-col">
                          <Input
                            v-model="item.schooName"
                            placeholder=""
                          />
                        </Col>
                      </Row>
                      <Row>
                        <Col :span="18" class="jm-edu-col">
                          <Input
                            v-model="item.majorName"
                            placeholder=""
                          />
                        </Col>
                      </Row>
                    </Row>
                    <Row>
                      <Col class="jm-edu-col">
                        <Input
                          v-model="item.eduContent"
                          type="textarea"
                          clearable
                          autosize
                          class="input-introduce"
                          placeholder=""
                        />
                      </Col>
                    </Row>
                  </Row>
                </div>
              </rightContent>
              <!--  -->
              <rightContent
                :themeList="workThemeList"
                themeFlag="work"
                key="3"
                name="edu"
                v-on:addEdus="addEdus('experience')"
                title=""
              >
                <div slot="slotRight">
                  <Row v-for="(item, index) in experienceList" :key="index">
                    <Row>
                      <Col :span="12" class="jm-edu-col">
                        <Input
                          v-model="item.date"
                          placeholder=":2016/08-2017/09"
                        />
                      </Col>
                      <Col :span="12" class="jm-edu-col">
                        <Input v-model="item.name" placeholder="" />
                      </Col>
                    </Row>
                    <Row>
                      <Col :span="18" class="jm-edu-col">
                        <Input
                          v-model="item.positionName"
                          placeholder=""
                        />
                      </Col>
                    </Row>
                    <Row>
                      <Col class="jm-edu-col">
                        <Input
                          v-model="item.content"
                          clearable
                          autosize
                          type="textarea"
                          class="input-introduce"
                          placeholder=""
                        />
                      </Col>
                    </Row>
                  </Row>
                </div>
              </rightContent>
              <!--  -->
              <rightContent
                :themeList="ThemeAppraiseList"
                themeFlag="appraise"
                key="4"
                name="appraise"
                title=""
              >
                <div slot="slotRight">
                  <Row>
                    <Col class="jm-edu-col">
                      <Input
                        v-model="selfEvaluation"
                        placeholder="500"
                        clearable
                        autosize
                        type="textarea"
                        class="input-introduce"
                      />
                    </Col>
                  </Row>
                </div>
              </rightContent>
            </vuedraggable>
          </Row>
        </Row>
      </div>
    </Row>
    <!--  -->
    <!-- <Modal
      class="warn-has-resume"
      v-model="modal2"
      width="360"
      :mask-closable="false"
    >
      <p slot="header" style="color:#2db7f5;text-align:center">
        <Icon type="ios-information-circle"></Icon>
        <span></span>
      </p>
      <div style="text-align:center">
        <p></p>
        <p></p>
      </div>
      <div slot="footer">
        <Button type="info" size="large" @click="cancle"></Button>
        <Button
          type="info"
          size="large"
          :loading="modal_loading"
          @click="getTemplatesResume"
          ></Button
        >
      </div>
    </Modal> -->
    <div v-if="showFind!=='1'">
    <findResumeModal :modal2="modal2" :modal_loading="modal_loading"></findResumeModal>
  </div>
   </div>
</template>

<script>
import jmUploadImg from "@/components/UploadImg";
import skillModal from "./SkillModal";
import baseInfoModel from "./BaseInfoModel";
import rightContent from "./common/RightContent";
import findResumeModal from "./common/FindResumeModal";
import Bus from "@/assets/event-bus.js";
import vuedraggable from "vuedraggable";
import headContent from "@/components/HeadContent";
// import html2canvas from "html2canvas";
import domtoimage from "dom-to-image";
export default {
  name: "Template3",
  components: {
    jmUploadImg,
    skillModal,
    baseInfoModel,
    rightContent,
    vuedraggable,
    headContent,
    findResumeModal
  },
  data() {
    return {
      modal2: false,
      modal_loading: false,
      // contentHight:"",//
      showHeadImg: true, //
      // showSetting: false, //
      settingObj: {
        isShowJobTime: true
      }, //
      // themeList:{},//
      jobThemeList: {}, //
      eduThemeList: {}, //
      workThemeList: {}, //
      ThemeAppraiseList: {}, //
      baseObjC: {}, //
      // resumeTemplateObj: {}, //
      // wirteIdeContent.vue
      modalSkill: false, //
      modalBaseInfo: false, //
      isBase: false, //base
      isSkill: false, //
      isBaseLine: false, //
      // isBaseLine: false, //
      formData: {
        showDescribe: true //
      }, //
      hasSkillList: [], //
      ImgBase64: "", //
      baseInfoList: [
        //
        {
          type: "ios-contact",
          baseText: "",
          inputText: ""
        },
        {
          type: "md-briefcase",
          baseText: "",
          inputText: ""
        },
        {
          type: "ios-call",
          baseText: "",
          inputText: ""
        },
        {
          type: "ios-mail",
          baseText: "",
          inputText: ""
        }
      ],
      jobIntentionList: [
        //
        {
          type: "ios-contact",
          baseText: "",
          name: "choseJob"
        },
        {
          type: "md-briefcase",
          baseText: "",
          name: "city"
        },
        {
          type: "ios-mail",
          baseText: "",
          name: "entryTime"
        },
        {
          type: "ios-call",
          baseText: "",
          name: "salary"
        }
      ],
      // 
      eduList: [
        {
          eduDate: "",
          schooName: "",
          majorName: "",
          eduContent: ""
        }
      ],
      // 
      experienceList: [
        {
          date: "",
          name: "",
          positionName: "",
          content: ""
        }
      ],
      // 
      selfEvaluation: ""
    };
  },
    props:["showFind"],
  watch: {
    contentHight: {
      handler(newVal, oldVal) {
        if (newVal !== oldVal) {
          console.log(newVal);
        }
      },
      deep: true
    }
  },
  methods: {
    // cancle() {
    //   this.modal2 = false;
    // },
    // // 
    // domToImg(){
    //    let doms = document.getElementById("code")
    //    this.common.transformImage(doms).then(dataUrl=>{
    //           this.resumeUrl = dataUrl
    //   })
    // },
    transformImage() {
      let doms = document.getElementById("code");
      this.common.transformImage(doms).then(dataUrl => {
        this.$router.push({ name: "preview", params: { dataUrl: dataUrl } });
      });
    },
    // 
    // 
    getTemplatesResume() {
      this.modal_loading = true;
      this.axios
        .get("resumeTemplates/getTemplatesResume", {
          params: {
            userName: localStorage.getItem("userName"), //vuex
            TemplateId: this.$route.query.id
          }
        })
        .then(res => {
          if (res.data.status == "0") {
            this.modal_loading = false;
            this.modal2 = false;
            var resumeTemplateObj = res.data.result.resumeContent;
            //  null.xxx
            if (!resumeTemplateObj) {
              return;
            }
            this.getTemplatesResumes(resumeTemplateObj);
          }
        })
        .catch(err => {});
    },
    // 
    getTemplatesResumes(resumeTemplateObj) {
      // var resumeTemplateObj = resumeTemplateObj.resumeTemplate[0].resumeContent;
      if (resumeTemplateObj.baseInfoList.headPic == "") {
        this.showHeadImg = true;
      } else if (resumeTemplateObj.baseInfoList.headPic == "") {
        this.showHeadImg = false;
      }
      console.log(resumeTemplateObj, "====");
      // 
      this.baseObjC = {
        name: resumeTemplateObj.baseInfoList.name,
        birthday: resumeTemplateObj.baseInfoList.birthday,
        age: resumeTemplateObj.baseInfoList.age, //
        tel: resumeTemplateObj.baseInfoList.tel,
        mail: resumeTemplateObj.baseInfoList.mail,
        work: resumeTemplateObj.baseInfoList.work, //
        headPic: resumeTemplateObj.baseInfoList.headPic, //
        wordDescribe: resumeTemplateObj.baseInfoList.wordDescribe, //
        showDescribe: resumeTemplateObj.baseInfoList.showDescribe //
      };
      // 
      (this.baseInfoList = [
        //
        {
          type: "ios-contact",
          baseText: "",
          inputText: resumeTemplateObj.baseInfoList.age || ""
        },
        {
          type: "md-briefcase",
          baseText: "",
          inputText: resumeTemplateObj.baseInfoList.work || ""
        },
        {
          type: "ios-call",
          baseText: "",
          inputText: resumeTemplateObj.baseInfoList.tel || ""
        },
        {
          type: "ios-mail",
          baseText: "",
          inputText: resumeTemplateObj.baseInfoList.mail || ""
        }
      ]),
        (this.formData = resumeTemplateObj.baseInfoList);
      this.hasSkillList = resumeTemplateObj.SkillList;
      this.jobIntentionList = resumeTemplateObj.jobIntentionList;
      this.eduList = resumeTemplateObj.eduList;
      this.experienceList = resumeTemplateObj.experienceList;
      this.selfEvaluation = resumeTemplateObj.selfEvaluation;
    },
    // 
    saveContent(nameId) {
      let doms = document.getElementById("code");
      this.common.transformImage(doms).then(dataUrl => {
        // 
        var content = {
          userName: localStorage.getItem("userName"), //
          hasCommonResume: false, //

          resumeTemplate: [
            //
            {
              TemplateId: nameId, //ID
              img: {
                url: dataUrl,
                name: nameId
              },
              resumeContent: {
                //
                baseInfoList: this.formData, //
                SkillList: this.hasSkillList, //
                jobIntentionList: this.jobIntentionList, //
                eduList: this.eduList, //
                experienceList: this.experienceList, //
                selfEvaluation: this.selfEvaluation //
              }
            }
          ]
        };
        this.axios
          .post("resumeTemplates/resumeTemplates", {
            content: content,
            TemplateId: nameId
          })
          .then(res => {
            if (res.data.status == "0") {
              // this.$Message.success("");
              Bus.$emit("showMoreResume");
            }
          })
          .catch(err => {
            console.log("err", err);
          });
      });
    },
    saveContentss() {
      Bus.$on("saveContents", () => {
        this.saveContent(this.$route.query.id);
      });
    },
    // 
    // Bus.$off("saveTheme")
    // 
    saveThemes() {
      Bus.$on("saveTheme", themeList => {
        var flags = themeList[1]; //
        console.log(themeList, "");
        if (flags == "jobIntention") {
          this.jobThemeList = themeList[0];
        } else if (flags == "edu") {
          this.eduThemeList = themeList[0];
        } else if (flags == "work") {
          this.workThemeList = themeList[0];
        } else if (flags == "appraise") {
          this.ThemeAppraiseList = themeList[0];
        }
      });
    },
    // 
    getSetting() {
      Bus.$on("changeSetting", settingObj => {
        this.settingObj = settingObj;
      });
    },
    // 
    enter(flag) {
      switch (flag) {
        case "base":
          this.isBase = true;
          break;
        case "skill":
          this.isSkill = true;
          break;
        case "baseRight":
          this.isBaseLine = true;
          break;
      }
    },
    //
    leave(flag) {
      switch (flag) {
        case "base":
          this.isBase = false;
          break;
        case "skill":
          this.isSkill = false;
          break;
        case "baseRight":
          this.isBaseLine = false;
          break;
      }
    },
    // 
    displaySkillModel() {
      this.modalSkill = true;
    },
    // 
    displayModelBase(name) {
      if (name == "base") {
        this.modalBaseInfo = true;
      }
      //  else if (name == "headIcon") {
      //   this.showSetting = true;
      // }
    },
    // 
    changeSkillModel(data) {
      this.modalSkill = false;
      this.modalBaseInfo = false;
    },
    // 
    saveBaseInfo(data) {
      console.log(data, "data=====");
      for (let item in data) {
        switch (item) {
          case "birthday":
            this.baseInfoList.find(item => item.baseText == "").inputText =
              data.age + "";
            break;
          case "work":
            this.baseInfoList.find(
              item => item.baseText == ""
            ).inputText = data.work;
            break;
          case "tel":
            this.baseInfoList.find(item => item.baseText == "").inputText =
              data.tel;
            break;
          case "mail":
            this.baseInfoList.find(item => item.baseText == "").inputText =
              data.mail;
            break;
        }
        if (data.headPic == "") {
          this.showHeadImg = true;
        } else if (data.headPic == "") {
          this.showHeadImg = false;
        }
        this.formData = data;
      }
    },
    // 
    saveSkill(data) {
      this.hasSkillList = data;
    },
    // 
    savaJobIntention(val) {
      this.jobIntentionList.find(item => item.name == "choseJob").baseText =
        val.choseJob;
      this.jobIntentionList.find(item => item.name == "city").baseText =
        val.city;
      this.jobIntentionList.find(item => item.name == "entryTime").baseText =
        val.entryTime;
      this.jobIntentionList.find(item => item.name == "salary").baseText =
        val.salary0 + 10 + "k ~ " + (val.salary1 + 10) + "k";
      console.log(this.jobIntentionList);
    },
    // 
    addEdus(name) {
      if (name == "edu") {
        var objEdu = {
          eduDate: "",
          schooName: "",
          majorName: "",
          eduContent: ""
        };
        this.eduList.push(objEdu);
      }
      if (name == "experience") {
        var objExperience = {
          date: "",
          name: "",
          positionName: "",
          content: ""
        };
        this.experienceList.push(objExperience);
      }
    },
    // 
    findHasResume() {
      this.axios
        .get("resumeTemplates/findHasResume", {
          params: {
            userName: localStorage.getItem("userName"), //vuex
            TemplateId: this.$route.query.id
          }
        })
        .then(res => {
          if (res.data.status == "0") {
            this.modal2 = true;
          }
        })
        .catch(err => {
          this.modal2 = false;
        });
    }
  },
  mounted() {
 if(this.showFind==="1"){
      this.getTemplatesResume()
    }else{
      this.findHasResume()
    }
    Bus.$on("getShowHeadImg", value => {
      if (value == "") {
        this.showHeadImg = true;
      } else if (value == "") {
        this.showHeadImg = false;
      }
    });
    // 
    Bus.$on("postPhotoBase64", ImgBase64 => {
      this.formData.ImgBase64 = ImgBase64;
    });
    Bus.$on("previews", () => {
      this.transformImage();
    });
    // 
    this.contentHight = this.$refs.rightContent.offsetHeight;
    console.log(this.contentHight);
    this.getSetting();
    //   // 
    //  this.getTemplatesResume();
    // 
    this.saveContentss();
    // 
    // Bus.$off("saveTheme")
    // 
    this.saveThemes();
    Bus.$on("saveRecommend", id => {
      if (id == 3) {
        this.saveContent(3);
      }
    });
    Bus.$on("saveRecommend", id => {
      if (id == 3) {
        this.saveContent(3);
      }
    });
    
    Bus.$on("showFindModal",data=>{
      this.modal2 = false
    })
   
    Bus.$on("getResumes",()=>{
      // 
    this.getTemplatesResume();
    })
  },
  // $on
  beforeDestroy() {
    Bus.$off("saveContentss");
  }
};
</script>


<style lang="less" scoped>
// 
.jm-headImg {
  position: relative;
  display: block;
  width: 170px;
  margin: 0 auto;
  padding: 20px;
  border: 1px dashed transparent !important;
  .head-icon {
    position: relative;
    width: 128px;
    height: 156px;
    margin: 0 auto;
    border: 4px solid #e5e5e5;
    background: #e5e5e5;
    overflow: hidden;
    cursor: pointer;
    // .jm-upload-icon {
    //   // background: url(./../../../../static/image/bg3.jpg);
    //   position: absolute;
    //   // z-index: -1;
    //   width: 40px;
    //   height: 40px;
    //   margin-left: 40px;
    //   margin-top: 55px;
    // }
  }
}
/deep/.ivu-input {
  border: none !important;
}
// 
.jm-headImg-lineIs {
  border: 1px dashed #00c091 !important;
}
.jm-head-icon {
  position: absolute;
  top: 0;
  right: 0;
  color: #fff;
  background: #00c091;
  margin: 5px;
  z-index: 1;
  cursor: pointer;
}
body {
  min-width: 1240px;
}
.jm-template {
  margin: 0 auto;
  width: 814px;
  min-height: 1160px;
  .resume-left {
    float: left;
    height: 1160px;
    width: 264px;
    background: #254665;
    padding: 20px 30px 40px 30px;
  }
  // 
  .jm-baseInfo {
    padding: 20px 0 20px 4px;
    width: 210px;
    color: #fff;
    font-size: 14px;
    border: 1px dashed transparent;
    .jm-age {
      height: 24px;
      line-height: 24px;
      .jm-baseText {
        margin-left: 5px;
      }
    }
  }
  // 
  .jm-skill-title {
    font-size: 20px;
    font-weight: bold;
  }
  .jm-skill-content {
    .jm-circle {
      width: 80px;
      margin: 20px 10px 30px 10px;
      .jm-circle-text {
        top: 60px;
        left: 10px;
        position: absolute;
      }
    }
  }
}
// 
.resume-right {
  // padding: 10px 30px;
  background: #fff;
  float: right;
  min-height: 1160px;
  padding: 10px 30px 40px 30px;
  width: 550px;
  .ivu-input {
    border: none !important;
  }
}
.jm-base-name {
  position: relative;
  padding-bottom: 15px;
  .jm-defult-line {
    border: 1px dashed transparent;
  }
  .jm-base-linehover0 {
    border: 1px dashed #00c091;
  }
  /deep/.ivu-input {
    border: none;
  }
  .jm-name {
    /deep/.ivu-input {
      font-size: 30px;
    }
  }
}
.jm-base-linehover {
  border: 1px dashed #00c091;
}
// 
.jobList {
  width: 25%;
  display: inline-block;
  padding: 20px 5px;
  .jobIcon {
    color: #747474;
    margin-top: -2.5px;
  }
  .jobText {
    color: #747474;
    font-size: 14px;
  }
}
.right-name {
