<template>
  <div>
    <!--  -->

    <div
      class="jm-theme"
      @mouseover="getHover(index)"
      v-for="(item, index) in list"
      :key="index"
    >
      <Row :class="indexZ == index ? 'jm-theme-hover' : ''">
        <span v-if="indexZ == index" @click="save(index)" class="jm-save-btn"
          ></span
        >
        <Row>
          <!--  -->
          <div class="title">
            <!-- <Icon type="ios-wine" size="32"/> -->
            <div
              class="jm-title-top"
              :class="[item.lineBottom, item.textDirection]"
            >
              <!--  -->
              <Icon
                size="30"
             
                :type="item.icon"
                v-if="item.isIcon"
              />
              <!--  -->
              <span v-if="item.lineIcon" :class="item.lineLeft"></span>
              <!--  -->
              <span v-if="item.lineIcon" class="line-icon-sides-left">
                <Icon type="md-wine" />
              </span>
              <!--  -->
              <span class="title-row" :class="item.lineIcon">{{ title }}</span>
              <!--  -->
              <span v-if="item.lineIcon" class="line-icon-sides-right">
                <Icon type="md-wine" />
              </span>
              <!--  -->
              <span v-if="item.lineIcon" :class="item.lineRight"></span>
            </div>
            <!-- <Icon type="ios-wine" /> -->
          </div>
        </Row>
      
      <slot name="slotTheme"></slot>
      <!-- <Row
          class="jobList"
          v-for="(item0, index0) in jobIntentionList"
          :key="index0"
        >
          <Icon :size="20" class="jobIcon" :type="item0.type" />
          <span class="jobText">{{ item0.baseText }}</span>
        </Row> -->
        </Row>
    </div>
  </div>
</template>
<script>
import Bus from "@/assets/event-bus.js";
// import modal3 from "./Modal3";
export default {
  name: "theme3",
  props: ["title", "themeFlag"],
  components: {
    // modal3
  },
  data() {
    return {
      // jmThemeHover:"jm-theme-hover",//hover
      indexZ: "", //
      list: [
        {
          isIcon: false, //
          icon: "md-list-box", //
          textDirection: "", //
          lineLeft: "both-sides-left", //,
          lineRight: "both-sides-right", //,
          lineBottom: "", //
          lineIcon: "line-icon", //
          isShowTheme: true //
        },
        {
          isIcon: true,
          icon: "ios-bicycle",
          textDirection: "left",
          lineLeft: "",
          lineBottom: "line-bottom", //
          lineIcon: "", //
          isShowTheme: true //
        },
        {
          isIcon: true,
          icon: "ios-body",
          textDirection: "center",
          lineLeft: "",
          lineBottom: "line-bottom-sort", //
          lineIcon: "", //
          isShowTheme: true //
        }
      ],
      // title: "",
      jobIntentionList: [
        //
        {
          type: "ios-contact",
          baseText: "",
          name: "choseJob"
        },
        {
          type: "md-briefcase",
          baseText: "",
          name: "city"
        },
        {
          type: "ios-mail",
          baseText: "",
          name: "entryTime"
        },
        {
          type: "ios-call",
          baseText: "",
          name: "salary"
        }
      ]
    };
  },
  watch: {},
  methods: {
    getHover(index) {
      this.indexZ = index;
    },
    save(index) {
      // console.log(index,this.list[index],"")

      Bus.$emit("saveTheme", [this.list[index], this.themeFlag]);
      Bus.$emit("closeTheme");
    }
  }
};
</script>
 <style lang="less" scoped>
/deep/.ivu-input {
  border: none;
  // color: #fff;
  // background: #e0e0e0;
  // opacity: .2;
}

// 
.jm-theme {
  // width:540px;
  // 
  height: auto;
  border: 1px solid #e8e8e8;
  margin-bottom: 20px;
  padding: 20px;
  .jobList {
    // width: 25%;
    display: inline-block;
    padding: 20px 5px;
    .jobIcon {
      color: #747474;
      margin-top: -2.5px;
    }
    .jobText {
      color: #747474;
      font-size: 14px;
    }
  }
  // 
  .title {
    width: 507px;
    // margin-top:-30px;
    // margin-left:30px;
    display: inline-block;
    .jm-title-top {
      display: inline-block;
      width: 507px;
      position: relative;
    }
    // 
    .line-bottom {
      border-bottom: 1px solid #254665;
    }
    // 
    .line-bottom-sort {
      border-bottom: 1px solid #254665;
    }

    // input,
    .title-row {
      width: 100px;
      display: inline-block;
      font-size: 20px;
      font-weight: bold;
      // margin-left:212px;
      position: relative;
      /deep/ .ivu-input {
        color: #254665;
        font-size: 20px;
        font-weight: bold;
      }
    }
    // 
    .center {
      text-align: center;
    }
    .right {
      text-align: right;
    }
    .left {
      text-align: left;
    }
  }
  // 
  .both-sides-left {
    width: 167px;
    display: inline-block;
    content: "";
    border-color: #254665;
    position: absolute;
    z-index: 100;
    // left: 0;
    // right: 172px;
    top: 50%;
    margin-top: -1px;
    border-bottom: 1px solid;
  }
  .both-sides-right {
    width: 167px;
    display: inline-block;
    content: "";
    border-color: #254665;
    position: absolute;
    z-index: 100;
    // left: 0;
    right: 0;
    top: 50%;
    margin-top: -1px;
    border-bottom: 1px solid;
  }
  // .both-sides-left::after {
  //   width:172px;
  //   content: "";
  //   border-color: #254665;
  //   position: absolute;
  //   // left: 70%;
  //   right: 0;
  //   top: 50%;
  //   margin-top: -1px;
  //   border-bottom: 1px solid;
  // }
}

// 
.line-icon-sides-left {
  display: inline-block;
  margin-left: 172px;
  text-align: center;
  width: 30px;
}
.line-icon-sides-right {
  display: inline-block;
  // margin-right:172px;
  text-align: center;
  width: 30px;
}
.jm-theme-hover {
  /deep/.ivu-input {
    border: none;
    // color: #fff;
    background: #e0e0e0;
    // opacity: .2;
  }
}
// ,
.jm-theme-hover::before {
  content: "";
  position: absolute;
  top: -20px;
  left: -20px;
  width: 538px;
  height: calc(100% + 40px);
  background: #666;
  opacity: 0.2;
}
.jm-save-btn {
  position: absolute;
  background-color: #00c190;
  color: white;
  border-radius: 4px;
  border: none;
  width: 60px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  vertical-align: middle;
  left: 50%;
  margin-left: -30px;
  top: 50%;
  z-index: 1000;
  cursor: pointer;
}
</style>