<template>
  <!--  -->
  <!-- propsmodalTipsv-model
  v-modelpropspropsvueprops
  dataisshow-->
  <div v-if="modalTips">
    <!-- <Button class="bntn" type="primary" @click="modal1 = true">Display dialog box</Button> -->
    <Modal
      v-model="isshow"
      title="3"
      @on-ok="ok"
      @on-cancel="cancel"
      :mask-closable="false"
    >
      <div class="zm-banner">
        <div @mouseenter="templateMask=3"
        @mouseleave="templateMask=0" class="template-recom" :class="templateMask==3?'template-mask':''">
          <h2 class="jm-title">
            3
          </h2>
           <div @click="saveTemplate(3)" class="jm-save-btn"></div>
           <div @click="gotoResumeTemplate(index)" class="jm-save-btn jm-router"
          ></div>
          <Template3 showFind="1" class="jm-content"></Template3>
        </div>

        <div class="jm-border"></div>
        <div class="template-recom" @mouseenter="templateMask=4"
        @mouseleave="templateMask=0" :class="templateMask==4?'template-mask':''">
          <h2 class="jm-title">4</h2>
           <div @click="saveTemplate(4)" class="jm-save-btn"></div>
           <div @click="gotoResumeTemplate(index)" class="jm-save-btn jm-router"
          ></div>
          <Template4 showFind="1" class="jm-content"  ></Template4>
        </div>
        <!-- <div></div> -->
      </div>
    </Modal>
  </div>
</template>
<script>
// import Swiper from "swiper";
// import "swiper/dist/css/swiper.min.css";
import Bus from "@/assets/event-bus.js";
// Template3Template4getTemplatesResume
// TemplateIdidTemplate3id3
// 
import Template3 from "./../WriteResumeTemplate/Template3";
import Template4 from "./../WriteResumeTemplate/Template4";
export default {
  name: "recommendResume",
  props: ["modalTips"],
  components: {
    Template3,
    Template4
  },
  data() {
    return {
      isshow: true,
      templateMask:"",//
    };
  },
  watch: {
    modalTips: {
      handler(newVal, oldVal) {
        console.log(newVal);
        newVal ? (this.isshow = true) : (this.isshow = false);
      },
      deep: true
    }
  },
  methods: {
    // 
    saveTemplate(id){
      // 
      Bus.$emit("saveRecommend",id)
    },
    // 
    gotoResumeTemplate(index){
      console.log(index+1,"")
      this.$router.push({
        // name: "writeResumeTemplate"+(index+1)
        name: "writeResumeIde",
        query:{
          id:index+1
        }
 
      });
      // 
       window.location.reload();
    },
    ok() {
      Bus.$emit("closeModel", false);
    },
    cancel() {
      Bus.$emit("closeModel", false);
    },
    //   enter(name) {
    //     console.log(111)
    //   this.templateMask = name;
    // },
    // leave(name) {
    //   // this.templateMask = 0;
    // },
  },
  mounted() {
    // var mySwiper = new Swiper(".swiper-container", {
    //   //   autoplay: true,
    //   //   delay: 2000, //2
    //   //   loop: true,
    //   // 
    //   pagination: {
    //     el: ".swiper-pagination",
    //     scrollbar: ".swiper-scrollbar",
    //     // 
    //     pagination: ".swiper-pagination",
    //     // 
    //     nextButton: ".swiper-button-next",
    //     prevButton: ".swiper-button-prev"
    //   }
    // });
  }
};
</script>
<style lang="less" scoped>
.bntn {
  position: absolute;
}
/deep/ .ivu-modal-body {
  min-height: 340px;
  height: 500px;
  overflow: auto;
}
/deep/.ivu-modal {
  width: 900px !important;
}
// banner
.zm-banner {
  z-index: 100;
  .jm-border {
    border: 10px solid #fff;
  }
  .template-recom {
    // text-align: center;
    position: relative;
    padding-bottom: 15px;
    // background: #ddd;
    .jm-title {
      text-align: center;
      background: #fff;
    }
  }
  // 
  .template-mask::before {
    content: "";
    position: absolute;
    z-index: 1;
    top: 31px;
    left: 18px;
    width: 814px;
    height: calc(100% + -46px);
    background: #666;
    opacity: 0.2;
  }
}
 .jm-save-btn {
  position: absolute;
  padding: 5px;
  background-color: #00c190;
  color: white;
  border-radius: 4px;
  border: none;
  // width: 60px;
  // height: 30px;
  line-height: 30px;
  text-align: center;
  vertical-align: middle;
  left: 50%;
  margin-left: -30px;
  top: 50%;
  z-index: 1000;
  cursor: pointer;
}
.jm-router{
  top: 55%;
}
